<template>
  <div class="panel panel-default" v-bind:class="getContainerClass()">
    <div class="panel-heading">
      <player-heading
        v-bind:results="state.results"
        v-bind:playerOneOrTwo="playerOneOrTwo"
        v-on:removePlayer="emit('removePlayer')"
        v-on:clearRange="emit('clearRange')"
      ></player-heading>
    </div>
    <div class="panel-body">
      <section class="left-or-top clearfix">
        <grid-selector
          v-bind:playerState="state"
          v-on:addHandToRange="emit('addHandToRange', $event)"
          v-on:removeHandFromRange="emit('removeHandFromRange', $event)"
        ></grid-selector>
        <slider-selector
          v-bind:playerState="state"
          v-bind:numPlayers="numPlayers"
          v-on:handleSliderChange="emit('handleSliderChange', $event)"
        ></slider-selector>
        <text-selector
          v-bind:hands="state.hands"
          v-on:updateRangeAccordingToText="emit('updateRangeAccordingToText', $event)"
        ></text-selector>
      </section>
      <section class="right-or-bottom clearfix">
        <tier-selector
          v-bind:hands="state.hands"
          v-bind:numPlayers="numPlayers"
          v-on:addTierToRange="emit('addTierToRange', $event)"
          v-on:removeTierFromRange="emit('removeTierFromRange', $event)"
        ></tier-selector>
        <category-selector
          v-bind:hands="state.hands"
          v-bind:numPlayers="numPlayers"
          v-on:addCategoryToRange="emit('addCategoryToRange', $event)"
          v-on:removeCategoryFromRange="emit('removeCategoryFromRange', $event)"
        ></category-selector>
      </section>
    </div>
  </div>
</template>

<script>
import PlayerHeading from '@/components/player/player-heading';
import GridSelector from '@/components/player/grid-selector';
import SliderSelector from '@/components/player/slider-selector';
import TextSelector from '@/components/player/text-selector';
import TierSelector from '@/components/player/tier-selector';
import CategorySelector from '@/components/player/category-selector';

export default {
  name: 'player',
  components: {
    'player-heading': PlayerHeading,
    'grid-selector': GridSelector,
    'slider-selector': SliderSelector,
    'text-selector': TextSelector,
    'tier-selector': TierSelector,
    'category-selector': CategorySelector
  },
  props: ['numPlayers', 'state', 'playerOneOrTwo'],
  methods: {
    emit: function (eventName, arg1) {
      this.$emit(eventName, arg1);
    },
    getContainerClass: function () {
      if (this.numPlayers === 1) {
        return 'one-player';
      } else if (this.numPlayers === 2 || this.numPlayers === 4) {
        return 'two-or-four-players';
      } else {
        return 'three-five-or-more-players';
      }
    }
  }
};
</script>

<style lang="scss" scoped>
  section {
    float: left;
  }

  section.left-or-top {
    max-width: 350px;
    margin-right: 10px;
  }

  @media (max-width: 1199px) and (min-width: 992px), (max-width: 672px) {
    .two-or-four-players section.right-or-bottom {
      margin-top: 10px;
    }
  }

  @media (min-width: 1200px), (max-width: 672px) {
    .three-five-or-more-players section.right-or-bottom {
      margin-top: 10px;
    }
  }

  @media (min-width: 1200px) {
    .three-five-or-more-players section.right-or-bottom {
      width: 340px;
    }
  }

  @media (max-width: 430px) {
    section.left-or-top {
      margin-right: 0px;
    }
  }
</style>



