import utilities from '@/services/utilities/utilities';
import constants from '@/services/constants';
import { getHandsFromText } from '@/services/text/get-hands-from-text';
import { checkTextValidity } from '@/services/text/validate';
import _ from 'lodash';

export default {
  handRankingsType: localStorage.getItem('handRankingType') || 'ten-handed-evolutionary',
  customHandRankings: localStorage.getItem('customHandRankings') || '',
  setHandRankingsType: function (newType, state) {
    this.handRankingsType = newType;
    state.handRankingsType = newType;
    localStorage.setItem('handRankingType', newType);
    constants.handStrings = constants.handRankings[newType];
    this._setHandRankings(state);
  },
  setCustomHandRankings: function (customHandRankingsString, state) {
    this.handRankingsType = 'custom';
    state.handRankingsType = 'custom';
    localStorage.setItem('handRankingType', 'custom');
    this.customHandRankings = customHandRankingsString;
    state.customHandRankings = customHandRankingsString;
    localStorage.setItem('customHandRankings', customHandRankingsString);
    constants.handStrings = customHandRankingsString.split(',');
    this._setHandRankings(state);
  },
  _setHandRankings: function (state) {
    constants.handStrings.forEach(function (handString, index) {
      constants.handStringIndexMap[handString] = index;
    });
    constants.specificHandStrings = [];
    constants.handStrings.forEach(function (handString) {
      var specificHands = utilities.getAllSpecificHands(handString);
      specificHands.forEach(function (specificHand) {
        constants.specificHandStrings.push(specificHand);
      });
    });

    if (state.players) {
      state.players.forEach(function (player) {
        player.hands = _.sortBy(player.hands, function (hand) {
          return constants.handStrings.indexOf(hand.string);
        });
      });
    } else {
      state.player.hands = _.sortBy(state.player.hands, function (hand) {
        return constants.handStrings.indexOf(hand.string);
      });
    }
  },
  clearPlayer: function (player) {
    player.numberOfSpecificHandsInRange = 0;
    player.hands.forEach(function (hand) {
      hand.inRange = false;
      for (var combo in hand.combos) {
        if (hand.combos.hasOwnProperty(combo)) {
          hand.combos[combo] = false;
        }
      }
    });
  },
  addHandToRange: function (handString, player) {
    if (handString.length === 4) {
      this.addSpecificHandToRange(handString, player);
      return;
    }

    var handIndex = constants.handStringIndexMap[handString];
    var hand = player.hands[handIndex];

    if (hand.inRange === 'partial') {
      for (var combo in hand.combos) {
        if (hand.combos.hasOwnProperty(combo)) {
          if (hand.combos[combo]) {
            hand.combos[combo] = false;
            player.numberOfSpecificHandsInRange--;
          }
        }
      }
    }

    hand.inRange = true;
    player.numberOfSpecificHandsInRange += hand.numCombos;
  },
  removeHandFromRange: function (handString, player) {
    var handIndex = constants.handStringIndexMap[handString];
    var hand = player.hands[handIndex];

    if (hand.inRange === true) {
      player.numberOfSpecificHandsInRange -= hand.numCombos;
    }

    else if (hand.inRange === 'partial') {
      for (var combo in hand.combos) {
        if (hand.combos.hasOwnProperty(combo)) {
          if (hand.combos[combo]) {
            hand.combos[combo] = false;
            player.numberOfSpecificHandsInRange--;
          }
        }
      }
    }

    hand.inRange = false;
  },
  addHandsToRange: function (handStrings, player) {
    for (var i = 0; i < handStrings.length; i++) {
      this.addHandToRange(handStrings[i], player);
    }
  },
  removeHandsFromRange: function (handStrings, player) {
    for (var i = 0; i < handStrings.length; i++) {
      this.removeHandFromRange(handStrings[i], player);
    }
  },
  addSpecificHandToRange: function (specificHandString, player) {
    specificHandString = utilities.normalizeSpecificHandString(specificHandString);
    var generalHandString = utilities.getGeneralHandFromSpecificHand(specificHandString);
    var generalHandIndex = constants.handStringIndexMap[generalHandString];
    var generalHand = player.hands[generalHandIndex];
    var suits = specificHandString[1] + specificHandString[3];
    var numCombosInRange = 0;

    if (generalHand.inRange === true) {
      return;
    }

    generalHand.inRange = 'partial';

    if (!generalHand.combos[suits]) {
      generalHand.combos[suits] = true;
      player.numberOfSpecificHandsInRange++;
    }

    // handle case where you add the last combo,
    // and now all combos are in the range
    for (var combo in generalHand.combos) {
      if (generalHand.combos.hasOwnProperty(combo)) {
        if (generalHand.combos[combo]) {
          numCombosInRange++;
        }
      }
    }

    if (numCombosInRange === generalHand.numCombos) {
      generalHand.inRange = true;

      for (var combo in generalHand.combos) {
        if (generalHand.combos.hasOwnProperty(combo)) {
          generalHand.combos[combo] = false;
        }
      }
    }
  },
  addTierToRange: function (tierNumber, player) {
    this.addHandsToRange(constants.tiers[tierNumber], player);
  },
  removeTierFromRange: function (tierNumber, player) {
    this.removeHandsFromRange(constants.tiers[tierNumber], player);
  },
  addCategoryToRange: function (categoryName, player) {
    this.addHandsToRange(constants.categories[categoryName], player);
  },
  removeCategoryFromRange: function (categoryName, player) {
    this.removeHandsFromRange(constants.categories[categoryName], player);
  },
  handleSliderChange: function (targetPercentage, player) {
    var currPercentage = (player.numberOfSpecificHandsInRange / 1326) * 100;
    var handIndex;
    targetPercentage = Number(targetPercentage);

    if (currPercentage < targetPercentage) {
      handIndex = 0;

      while (currPercentage < targetPercentage) {
        if (player.hands[handIndex].inRange !== true) {
          this.addHandToRange(player.hands[handIndex].string, player);
          currPercentage = (player.numberOfSpecificHandsInRange / 1326) * 100;
        }

        handIndex++;
      }
    } else {
      handIndex = 168;

      while (currPercentage > targetPercentage) {
        if (player.hands[handIndex].inRange === true || player.hands[handIndex].inRange === 'partial') {
          this.removeHandFromRange(player.hands[handIndex].string, player);
          currPercentage = (player.numberOfSpecificHandsInRange / 1326) * 100;
        }

        handIndex--;
      }
    }
  },
  updateRangeAccordingToText: function (text, player) {
    var checkTextValidityResult;
    var handsInRange;

    if (text.length === 0) {
      this.clearPlayer(player);
      return;
    }

    if ((text.split(', ').length === 1) && (text.split(',').length > 1)) {
      alert('Invalid input. There needs to be a space after the comma(s).');
      return;
    }

    checkTextValidityResult = checkTextValidity(text);
    if (!checkTextValidityResult.valid) {
      alert('Invalid inputs: ' + checkTextValidityResult.invalidParts.join(', '));
      return;
    }

    handsInRange = getHandsFromText(text);
    this.clearPlayer(player);
    this.addHandsToRange(handsInRange, player);
  }
}



