<template>
  <div>
    <table>
      <tbody>
        <tr v-for="row in grid">
          <td
            v-for="hand in row"
            v-bind:class="getClassString(hand)"
            v-on:mousedown="handleGridMouseDown(hand)"
            v-on:mouseover="handleGridMouseOver(hand)"
          >
            <span v-bind:class="{ 'hand-string-with-specific-suit': getDisplayType(hand) !== 'noSpecificSuit' }">{{ getDisplayString(hand) }}</span>
            <span class="suits">
              <!-- suited -->
              <span v-if="getDisplayType(hand) === 'hearts-hearts'" class="hearts">&hearts;</span>
              <span v-if="getDisplayType(hand) === 'diamonds-diamonds'" class="diamonds">&diams;</span>
              <span v-if="getDisplayType(hand) === 'spades-spades'">&spades;</span>
              <span v-if="getDisplayType(hand) === 'clubs-clubs'">&clubs;</span>

              <!-- offsuit + pairs -->
              <span v-if="getDisplayType(hand) === 'hearts-diamonds'">
                <span class="hearts">&hearts;</span>
                <span class="diamonds">&diams;</span>
              </span>
              <span v-if="getDisplayType(hand) === 'hearts-spades'">
                <span class="hearts">&hearts;</span>&spades;
              </span>
              <span v-if="getDisplayType(hand) === 'hearts-clubs'">
                <span class="hearts">&hearts;</span>&clubs;
              </span>
              <span v-if="getDisplayType(hand) === 'diamonds-hearts'">
                <span class="diamonds">&diams;</span><span class="hearts">&hearts;</span>
              </span>
              <span v-if="getDisplayType(hand) === 'diamonds-spades'">
                <span class="diamonds">&diams;</span>&spades;
              </span>
              <span v-if="getDisplayType(hand) === 'diamonds-clubs'">
                <span class="diamonds">&diams;</span>&clubs;
              </span>
              <span v-if="getDisplayType(hand) === 'spades-hearts'">
                &spades;<span class="hearts">&hearts;</span>
              </span>
              <span v-if="getDisplayType(hand) === 'spades-diamonds'">
                &spades;<span class="diamonds">&diams;</span>
              </span>
              <span v-if="getDisplayType(hand) === 'spades-clubs'">
                &spades;&clubs;
              </span>
              <span v-if="getDisplayType(hand) === 'clubs-hearts'">
                &clubs;<span class="hearts">&hearts;</span>
              </span>
              <span v-if="getDisplayType(hand) === 'clubs-diamonds'">
                &clubs;<span class="diamonds">&diams;</span>
              </span>
              <span v-if="getDisplayType(hand) === 'clubs-spades'">
                &clubs;&spades;
              </span>

              <!-- hover to see -->
              <span v-if="getDisplayType(hand) === 'moreThanOneCombo'" class="info">ℹ️</span>
            </span>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import utilities from '@/services/utilities/utilities';
import constants from '@/services/constants';

var exportObj = {
  name: 'grid-selector',
  props: ['playerState'],
  data: function () {
    return {
      grid: constants.grid
    }
  },
  methods: {
    getDisplayType: function (handString) {
      var inRangeValue = utilities.getInRangeValue(handString, this.playerState.hands);
      var handObj;
      var numCombosInRange = 0;
      var currComboInRange;
      var firstSuit;
      var secondSuit;
      var letterToSuitMap = {
        c: 'clubs',
        s: 'spades',
        h: 'hearts',
        d: 'diamonds'
      };

      if (inRangeValue === true || inRangeValue === false) {
        return 'noSpecificSuit';
      }

      handObj = this.playerState.hands[constants.handStringIndexMap[handString]];
      for (var suits in handObj.combos) {
        if (handObj.combos.hasOwnProperty(suits)) {
          if (handObj.combos[suits] === true) {
            currComboInRange = suits;
            numCombosInRange++;
          }
        }
      }

      if (numCombosInRange === 1) {
        firstSuit = letterToSuitMap[currComboInRange[0]];
        secondSuit = letterToSuitMap[currComboInRange[1]];
        return firstSuit + '-' + secondSuit;
      }

      else if (numCombosInRange > 1) {
        return 'moreThanOneCombo';
      }
    },
    getDisplayString: function (hand) {
      var displayString = hand[0].toUpperCase() + hand[1].toUpperCase();

      if (hand[2]) {
        displayString += hand[2];
      }

      return displayString;
    },
    getClassString: function (hand) {
      var classString = 'noselect';

      classString += ' ' + utilities.getHandType(hand);

      if (utilities.isHandInRange(hand, this.playerState.hands)) {
        classString += ' in-range';
      }

      return classString;
    },
    handleGridMouseDown: function (hand) {
      if (utilities.isHandInRange(hand, this.playerState.hands)) {
        this.$emit('removeHandFromRange', hand);
        this.playerState.clickContext = 'removing';
      } else {
        this.$emit('addHandToRange', hand);
        this.playerState.clickContext = 'adding';
      }
    },
    handleGridMouseOver: function (hand) {
      if (this.playerState.clickContext === 'adding' && !utilities.isHandInRange(hand, this.playerState.hands)) {
        this.$emit('addHandToRange', hand);
      } else if (this.playerState.clickContext === 'removing' && utilities.isHandInRange(hand, this.playerState.hands)) {
        this.$emit('removeHandFromRange', hand);
      }
    }
  }
};

export default exportObj;
</script>

<style lang="scss" scoped>
@import "node_modules/bootstrap-sass/assets/stylesheets/bootstrap/variables";
@import "src/assets/mixins";

table {
  border-spacing: 2px;
  border-collapse: separate;
  max-width: 100%;
}

td {
  @include noselect;
  text-align: center;
  vertical-align: middle;
  cursor: cell;
  position: relative;

  .hand-string-with-specific-suit, .suits {
    position: absolute;
    left: 50%;
    transform: translate(-50%, 0);
  }

  .hand-string-with-specific-suit {
    top: 0;
  }

  .suits {
    font-size: 13px;
    top: 9px;
    width: fit-content;
  }

  .hearts, .diamonds {
    color: red;
  }

  .info {
    font-size: 7px;
    position: relative;
    bottom: 1px;
  }
}

.suited {
  background-color: lighten($brand-success, 30%);
}
.offsuit {
  background-color: lighten($brand-danger, 30%);
}
.pair {
  background-color: lighten($gray-base, 82%);
}

.suited.in-range {
  background-color: lighten($brand-success, 10%);
}
.offsuit.in-range {
  background-color: lighten($brand-danger, 10%);
}
.pair.in-range {
  background-color: lighten($gray-base, 62%);
}

@media (min-width: 400px) {
  td {
    width: 25px;
    height: 25px;
    font-size: 10px;
    border-radius: 5px;
  }
}

@media (max-width: 399px) {
  .suits {
    display: none;
  }

  td .hand-string-with-specific-suit {
    position: static;
    font-style: italic;
  }
}

@media (max-width: 375px) {
  td {
    font-size: 10.5px;
    padding: 1px;
    border-radius: 3px;
  }
}

@media (max-width: 360px) {
  td {
    font-size: 10px;
  }
}

@media (max-width: 320px) {
  td {
    font-size: 8.3px;
  }
}
</style>



