<style lang="scss" scoped>
  nav.navbar.navbar-default {
    margin-bottom: 15px;
  }

  .navbar-link {
    text-decoration: underline;
  }

  .router-link-exact-active {
    pointer-events: none;
    font-weight: bold;
    position: relative;
    bottom: 2px;

    &:focus {
