<template>
  <div>
    <div class="row">
      <div v-bind:class="getColumnClass()" v-for="(player, index) in state.players">
        <player
          v-bind:numPlayers="state.players.length"
          v-bind:state="state.players[index]"
          v-bind:playerOneOrTwo="index === 0 || index === 1"
          v-on:removePlayer="removePlayer(index)"
          v-on:addHandToRange="addHandToRange($event, index)"
          v-on:removeHandFromRange="removeHandFromRange($event, index)"
          v-on:addTierToRange="addTierToRange($event, index)"
          v-on:removeTierFromRange="removeTierFromRange($event, index)"
          v-on:addCategoryToRange="addCategoryToRange($event, index)"
          v-on:removeCategoryFromRange="removeCategoryFromRange($event, index)"
          v-on:handleSliderChange="handleSliderChange($event, index)"
          v-on:updateRangeAccordingToText="updateRangeAccordingToText($event, index)"
          v-on:clearRange="clearRange(index)"
        ></player>
      </div>
    </div>

    <div>
      <community-card-selector
        v-on:setCommunityCard="setCommunityCard($event)"
        v-bind:communityCards="state.communityCards"
      ></community-card-selector>

      <section class="simulate-to non-mobile">
        Simulate to:
        <section class="btn-group">
          <button
            class="btn btn-default"
            v-bind:class="{ 'active': state.simulateTo === 'flop' }"
            v-on:click="state.simulateTo = 'flop'"
          >
            Flop
          </button>
          <button
            class="btn btn-default"
            v-bind:class="{ 'active': state.simulateTo === 'turn' }"
            v-on:click="state.simulateTo = 'turn'"
          >
            Turn
          </button>
          <button
            class="btn btn-default"
            v-bind:class="{ 'active': state.simulateTo === 'river' }"
            v-on:click="state.simulateTo = 'river'"
          >
            River
          </button>
        </section>
      </section>

      <section class="simulate-to mobile">
        <p>Simulate to:</p>
        <section class="btn-group btn-group-justified">
          <div class="btn-group">
            <button
              class="btn btn-default"
              v-bind:class="{ 'active': state.simulateTo === 'flop' }"
              v-on:click="state.simulateTo = 'flop'"
            >
              Flop
            </button>
          </div>
          <div class="btn-group">
            <button
              class="btn btn-default"
              v-bind:class="{ 'active': state.simulateTo === 'turn' }"
              v-on:click="state.simulateTo = 'turn'"
            >
              Turn
            </button>
          </div>
          <div class="btn-group">
            <button
              class="btn btn-default"
              v-bind:class="{ 'active': state.simulateTo === 'river' }"
              v-on:click="state.simulateTo = 'river'"
            >
              River
            </button>
          </div>
        </section>
      </section>

      <button class="btn btn-default add-player" v-on:click="addPlayer()">Add Player</button>
      <button class="btn btn-default btn-block add-player-block" v-on:click="addPlayer()">Add Player</button>

      <simulate-button
        v-on:simulate="simulate()"
        v-bind:text="state.simulateButtonText"
        v-bind:numPlayers="state.players.length"
      ></simulate-button>
    </div>
  </div>
</template>

<script>
import Player from '@/components/player/player';
import CommunityCardSelector from '@/components/community-card-selector';
import SimulateButton from '@/components/simulate-button';
import equityCalculatorStore from '@/stores/equity-calculator';
import simulate from '@/services/simulator/simulate';

export default {
  name: 'equity-calculator',
  components: {
    player: Player,
    'simulate-button': SimulateButton,
    'community-card-selector': CommunityCardSelector
  },
  data: function () {
    return {
      state: equityCalculatorStore.state
    };
  },
  methods: {
    addPlayer: function () {
      equityCalculatorStore.addPlayer();
    },
    removePlayer: function (index) {
      equityCalculatorStore.removePlayer(index);
    },
    getColumnClass: function () {
      if (this.state.players.length === 2 || this.state.players.length === 4) {
        return 'col-md-6';
      }

      return 'col-lg-4';
    },
    addHandToRange: function (hand, playerIndex) {
      equityCalculatorStore.addHandToRange(hand, playerIndex);
    },
    removeHandFromRange: function (hand, playerIndex) {
      equityCalculatorStore.removeHandFromRange(hand, playerIndex);
    },
    addTierToRange: function (tierNumber, playerIndex) {
      equityCalculatorStore.addTierToRange(tierNumber, playerIndex);
    },
    removeTierFromRange: function (tierNumber, playerIndex) {
      equityCalculatorStore.removeTierFromRange(tierNumber, playerIndex);
    },
    addCategoryToRange: function (categoryName, playerIndex) {
      equityCalculatorStore.addCategoryToRange(categoryName, playerIndex);
    },
    removeCategoryFromRange: function (categoryName, playerIndex) {
      equityCalculatorStore.removeCategoryFromRange(categoryName, playerIndex);
    },
    handleSliderChange: function (targetPercentage, playerIndex) {
      equityCalculatorStore.handleSliderChange(targetPercentage, playerIndex);
    },
    updateRangeAccordingToText: function (text, playerIndex) {
      equityCalculatorStore.updateRangeAccordingToText(text, playerIndex);
    },
    clearRange: function (playerIndex) {
      equityCalculatorStore.clearPlayer(playerIndex);
    },
    setCommunityCard: function (params) {
      equityCalculatorStore.setCommunityCard(params.communityCard, params.cardString);
    },
    simulate: function () {
      simulate(equityCalculatorStore, this.state.players, this.state.communityCards, this.state.simulateTo, 5000);
    }
  }
};
</script>

<style lang="scss" scoped>
  .add-player {
    float: left;
    margin-left: 20px;
  }

  .add-player-block {
    display: none;
  }

  .simulate-to {
    float: left;
    margin-left: 20px;
  }

  .mobile {
    display: none;
  }

  @media (max-width: 661px) {
    .simulate-to.mobile {
      display: block;
    }

    .simulate-to {
      display: none;
      margin-left: 0;
      margin-bottom: 20px;
      margin-top: 20px;
    }

    .add-player-block {
      display: block;
      margin-top: 20px;
      margin-bottom: 20px;
    }

    .add-player {
      display: none;
    }

    .mobile {
      display: block;
    }

    .non-mobile {
      display: none;
    }
  }
</style>



