<template>
  <button
    class="btn btn-primary btn-block"
    v-bind:class="{ 'two-players': numPlayers === 2, 'one-player': numPlayers === 1 }"
    v-bind:disabled="text !== 'Simulate'"
    v-on:click="simulate()"
  >
    {{ text }}
  </button>
</template>

<script>
export default {
  name: 'simulate-button',
  props: ['text', 'numPlayers'],
  methods: {
    simulate: function () {
      this.$emit('simulate');
    }
  }
};
</script>

<style lang="scss" scoped>
  button:not(.two-players, .one-player) {
    margin-top: 75px;
  }

  @media (min-width: 1200px) {
    .two-players {
      width: 582px;
      float: left;
      margin-left: 15px;
      margin-top: 0;
    }

    .one-player {
      float: left;
      margin-left: 15px;
      width: 685px;
    }
  }


  @media (max-width: 661px) {
    button {
      margin-top: 0 !important;
    }

    .one-player {
      margin-left: 0;
      width: 100%;
    }
  }
</style>



