import createPlayer from '@/services/player-factory';
import utilities from '@/services/utilities/utilities';
import storeHelper from '@/stores/helpers';

export default {
  state: {
    simulateButtonText: 'Simulate',
    simulateTo: 'river',
    communityCards: {
      flop1: '',
      flop2: '',
      flop3: '',
      turn: '',
      river: ''
    },
    player: createPlayer(),
    handRankingsType: storeHelper.handRankingsType,
    customHandRankings: storeHelper.customHandRankings
  },
  reset: function () {
    this.state = {
      simulateButtonText: 'Simulate',
      simulateTo: 'river',
      communityCards: {
        flop1: '',
        flop2: '',
        flop3: '',
        turn: '',
        river: ''
      },
      player: createPlayer(),
      handRankingsType: storeHelper.handRankingsType,
      customHandRankings: storeHelper.customHandRankings
    };
  },

  // setters
  setSimulateButtonText: function (newText) {
    this.state.simulateButtonText = newText;
  },
  setClickContextToNull: function () {
    this.state.player.clickContext = null;
  },
  setCommunityCard: function (communityCard, cardString) {
    this.state.communityCards[communityCard] = cardString;
  },

  // outsourced to storeHelper
  setHandRankingsType: function (newType) {
    storeHelper.setHandRankingsType(newType, this.state);
  },
  setCustomHandRankings: function (customHandRankingsString) {
    storeHelper.setCustomHandRankings(customHandRankingsString, this.state);
  },
  clearPlayer: function () {
    storeHelper.clearPlayer(this.state.player);
  },
  addHandToRange: function (handString) {
    storeHelper.addHandToRange(handString, this.state.player);
  },
  removeHandFromRange: function (handString) {
    storeHelper.removeHandFromRange(handString, this.state.player);
  },
  addTierToRange: function (tierNumber) {
    storeHelper.addTierToRange(tierNumber, this.state.player);
  },
  removeTierFromRange: function (tierNumber) {
    storeHelper.removeTierFromRange(tierNumber, this.state.player);
  },
  addCategoryToRange: function (categoryName) {
    storeHelper.addCategoryToRange(categoryName, this.state.player);
  },
  removeCategoryFromRange: function (categoryName) {
    storeHelper.removeCategoryFromRange(categoryName, this.state.player);
  },
  handleSliderChange: function (targetPercentage) {
    storeHelper.handleSliderChange(targetPercentage, this.state.player);
  },
  updateRangeAccordingToText: function (text) {
    storeHelper.updateRangeAccordingToText(text, this.state.player);
  },

  // logic
  updateResults: function (playerStats, numberOfSimulations) {
    var bestHandProbabilities = this.state.player.results.bestHandProbabilities;
    var bestHandCounts = playerStats[0].bestHandCounts;

    bestHandProbabilities.highCard = bestHandCounts.highCard / numberOfSimulations;
    bestHandProbabilities.pair = bestHandCounts.pair / numberOfSimulations;
    bestHandProbabilities.twoPair = bestHandCounts.twoPair / numberOfSimulations;
    bestHandProbabilities.threeOfAKind = bestHandCounts.threeOfAKind / numberOfSimulations;
    bestHandProbabilities.straight = bestHandCounts.straight / numberOfSimulations;
    bestHandProbabilities.flush = bestHandCounts.flush / numberOfSimulations;
    bestHandProbabilities.fullHouse = bestHandCounts.fullHouse / numberOfSimulations;
    bestHandProbabilities.fourOfAKind = bestHandCounts.fourOfAKind / numberOfSimulations;
    bestHandProbabilities.straightFlush = bestHandCounts.straightFlush / numberOfSimulations;
    bestHandProbabilities.royalFlush = bestHandCounts.royalFlush / numberOfSimulations;
  }
};



