<script>
var dataObj = {
  selectedCommunityCard: ''
};

$(document).ready(function () {
  $('#community-cards').on('hidden.bs.modal', function (e) {
    dataObj.selectedCommunityCard = '';
  });
});

export default {
  name: 'community-card-selector',
  props: ['communityCards'],
  data: function () {
    return dataObj;
  },
  methods: {
    setCommunityCard: function (cardString, selectedCommunityCard) {
      selectedCommunityCard = selectedCommunityCard || this.selectedCommunityCard;

      this.$emit('setCommunityCard', {
        communityCard: selectedCommunityCard,
        cardString: cardString
      });
      $('#community-cards').modal('hide');
    },
    getCardImageSrc: function (cardString) {
      var src = '/static/';

      if (cardString[0] === 'a') {
        src += 'ace-of-';
      } else if (cardString[0] === 'k') {
        src += 'king-of-';
      } else if (cardString[0] === 'q') {
        src += 'queen-of-';
      } else if (cardString[0] === 'j') {
        src += 'jack-of-';
      } else if (cardString[0] === 't') {
        src += 'ten-of-';
      } else if (cardString[0] === '9') {
        src += 'nine-of-';
      } else if (cardString[0] === '8') {
        src += 'eight-of-';
      } else if (cardString[0] === '7') {
        src += 'seven-of-';
      } else if (cardString[0] === '6') {
        src += 'six-of-';
      } else if (cardString[0] === '5') {
        src += 'five-of-';
      } else if (cardString[0] === '4') {
        src += 'four-of-';
      } else if (cardString[0] === '3') {
        src += 'three-of-';
      } else if (cardString[0] === '2') {
        src += 'two-of-';
      }

      if (cardString[1] === 'h') {
        src += 'hearts';
      } else if (cardString[1] === 'd') {
        src += 'diamonds';
      } else if (cardString[1] === 's') {
        src += 'spades';
      } else if (cardString[1] === 'c') {
        src += 'clubs';
      }

      src += '.png';
      return src;
    },
    isCardAvailable: function (cardString) {
      if (this.communityCards.flop1 === cardString) {
        return false;
      } else if (this.communityCards.flop2 === cardString) {
        return false;
      } else if (this.communityCards.flop3 === cardString) {
        return false;
      } else if (this.communityCards.turn === cardString) {
        return false;
      } else if (this.communityCards.river === cardString) {
        return false;
      }

      return true;
    }
  }
};
</script>

<style lang="scss" scoped>
  .community-card-selector {
    float: left;
  }

  div {
    span {
      vertical-align: middle;
    }

    img {
      width: 100%;
    }

    .deselect {
      position: relative;
      left: 145px;

      span {
        position: relative;
        cursor: pointer;

        &:nth-child(2) {
          left: 18px;
        }

        &:nth-child(3) {
          left: 34px;
        }

        &:nth-child(4) {
          left: 51px;
        }

        &:nth-child(5) {
          left: 68px;
        }
      }
    }

    .community-card {
      display: inline-block;
      width: 24px;
      height: 32px;
      border: 1px solid lightgray;
      cursor: pointer;
    }

    .modal-dialog {
      width: 700px;
      max-width: 95%;
      margin-left: auto;
      margin-right: auto;

      table {
        border-spacing: 5px;
        border-collapse: separate;

        td {
          img {
            cursor: pointer;
            width: 100%;
          }
        }
      }
    }

    @media (max-width: 800px) {
      .modal-dialog {
        width: 100%;
      }
    }

    .mobile {
      display: none;
    }

    @media (max-width: 661px) {
      .modal-dialog {
        width: fit-content;
      }

      .mobile {
        display: block;

        img {
          width: 60px;
          margin-right: 20px;
          margin-bottom: 10px;
        }

        td:last-child img {
          margin-right: 0;
        }
      }

      .non-mobile {
        display: none;
      }

      .community-card-selector {
        display: block;
        float: none;
        margin-left: 0px;
      }

      .preset-community-cards-text {
        display: block;
      }

      .community-card {
        width: 54px;
        height: 72px;
      }

      .deselect {
        position: relative;
        left: 20px;

        span {
          position: relative;
          cursor: pointer;
          font-size: 26px;
          color: darkgray;

          &:nth-child(2) {
            left: 39px;
          }

          &:nth-child(3) {
            left: 78px;
          }

          &:nth-child(4) {
            left: 115px;
          }

          &:nth-child(5) {
            left: 153px;
          }
        }
      }
    }

    @media (max-width: 359px) {
      .modal-dialog .mobile img {
        width: 50px;
      }
    }
  }
</style>



