import { _isHandValid } from '@/services/text/validate';
import utilities from '@/services/utilities/utilities';

export { areCustomHandRankingsValid, areCustomTiersValid }

function areCustomHandRankingsValid(customHandRankingsString) {
  customHandRankingsString = customHandRankingsString.toLowerCase();
  customHandRankingsString = customHandRankingsString.replace(/, /g, ',');

  var customHandRankingsArray = customHandRankingsString.split(',');

  if (customHandRankingsArray.length !== 169) {
    return false;
  }

  if (utilities.hasDuplicates(customHandRankingsArray)) {
    return false;
  }

  for (var i = 0; i < 169; i++) {
    if (_isHandValid(customHandRankingsArray[i]) === false) {
      return false;
    }
  }

  return true;
}

function areCustomTiersValid(customTiers) {
  var customTierArray;
  var allHandsInTiers = [];

  for (var i = 0; i < customTiers.length; i++) {
    customTierArray = customTiers[i].toLowerCase().replace(/, /g, ',').split(',');

    for (var j = 0; j < customTierArray.length; j++) {
      allHandsInTiers.push(customTierArray[j]);
      if (!_isHandValid(customTierArray[j])) {
        return false;
      }
    }
  }

  if (utilities.hasDuplicates(allHandsInTiers)) {
    return false;
  }

  return true;
}



