import constants from '@/services/constants';
import utilities from '@/services/utilities/utilities';

export {
  checkTextValidity,
  _isTextPartValid,
  _isHandValid
}

function checkTextValidity(text) {
  text = text.toLowerCase();
  text = text.trim();

  var textParts = text.split(', '); // 'aa, jj-99' --> ['aa', 'jj-99']
  var invalidParts = [];

  textParts.forEach(function (textPart) {
    if (!_isTextPartValid(textPart)) {
      invalidParts.push(textPart);
    }
  });

  return {
    valid: invalidParts.length === 0,
    invalidParts: invalidParts
  };
}

/*
  valid text parts:
    [singleHand]
    [singleHand]+
      except aa+, aks+, 87s+, etc.
    [singleHand]-
      except 22-, a2s-, 82s-, etc.
    [singleHand]-[singleHand]
      except pair-suited, offsuit-suit, etc.
*/
function _isTextPartValid(textPart) {
  textPart = textPart.toLowerCase();
  var plusSplit = textPart.split('+');
  var dashSplit = textPart.split('-');
  var hasPlus = plusSplit.length > 1;
  var hasMinus = false;
  var hasDash = false;
  var rangeLeftType;
  var rangeRightType;

  // there's one dash
  if (dashSplit.length === 2) {
    // ends in a dash
    if (textPart[textPart.length - 1] === '-') {
      hasMinus = true;
    } else {
      hasDash = true;
    }
  }

  // [handPart]+
  if (hasPlus) {
    // + needs to be at the end
    if (plusSplit[1].length > 0) {
      return false;
    }

    // in [handPart]+, [handPart] needs to be valid
    if (!_isHandValid(plusSplit[0])) {
      return false;
    }

    // + not applicable for these
    if ([
      'aa+',
      'aks+', 'kqs+', 'qjs+', 'jts+', 't9s+', '98s+', '87s+', '76s+', '65s+', '54s+', '43s+', '32s+',
      'ako+', 'kqo+', 'qjo+', 'jto+', 't9o+', '98o+', '87o+', '76o+', '65o+', '54o+', '43o+', '32o+'
    ].indexOf(textPart) > -1) {
      return false;
    }

    // otherwise, [handPart]+ is valid
    return true;
  }

  // [handPart]-
  else if (hasMinus) {
    // - needs to be at the end
    if (dashSplit[1].length > 0) {
      return false;
    }

    // in [handPart]-, [handPart] needs to be valid
    if (!_isHandValid(dashSplit[0])) {
      return false;
    }

    // + not applicable for these
    if ([
      '22-',
      'a2s-', 'k2s-', 'q2s-', 'j2s-', 't2s-', '92s-', '82s-', '72s-', '62s-', '52s-', '42s-', '32s-',
      'a2o-', 'k2o-', 'q2o-', 'j2o-', 't2o-', '92o-', '82o-', '72o-', '62o-', '52o-', '42o-', '32o-'
    ].indexOf(textPart) > -1) {
      return false;
    }

    // otherwise, [handPart]- is valid
    return true;
  }

  // [handPart]-[handPart]
  else if (hasDash) {
    // should only be one dash in [handPart]-[handPart]
    if (dashSplit.length > 2) {
      return false;
    }


    if (textPart.length === 7) {
      // KJs-87s should be invalid
      if (textPart[0] !== textPart[4]) {
        return false;
      }

      // K7s-KJs should be invalid
      if (constants.ranks.indexOf(textPart[1]) > constants.ranks.indexOf(textPart[5])) {
        return false;
      }
    }

    if (textPart.length === 5) {
      // 55-99 should be invalid
      if (constants.ranks.indexOf(textPart[0]) > constants.ranks.indexOf(textPart[3])) {
        return false;
      }
    }

    // in [handPart]-[handPart], both handParts need to be valid
    if (!_isHandValid(dashSplit[0])) {
      return false;
    }
    if (!_isHandValid(dashSplit[1])) {
      return false;
    }

    if (dashSplit[0].indexOf('s') > -1) {
      rangeLeftType = 'suited';
    } else if (dashSplit[0].indexOf('o') > -1) {
      rangeLeftType = 'offsuit';
    } else {
       rangeLeftType = 'pair';
    }

    if (dashSplit[1].indexOf('s') > -1) {
      rangeRightType = 'suited';
    } else if (dashSplit[1].indexOf('o') > -1) {
      rangeRightType = 'offsuit';
    } else {
       rangeRightType = 'pair';
    }

    if (rangeLeftType === rangeRightType) {
      return true;
    } else {
      return false;
    }
  }

  // [handPart]
  else {
    return _isHandValid(textPart) || _isSpecificHandValid(textPart);
  }
}

function _isHandValid (handString) {
  handString = handString.toLowerCase();
  return constants.handStrings.indexOf(handString) > -1
}

function _isSpecificHandValid (handString) {
  if (handString.length !== 4) {
    return false;
  }

  handString = utilities.normalizeSpecificHandString(handString);
  return constants.specificHandStrings.indexOf(handString) > -1;
}



