import constants from '@/services/constants';
import utilities from '@/services/utilities/utilities';

export {
  getTextFromHands,
  _getTextComponent
}

function getTextFromHands(playerHands) {
  var textString = '';

  textString += _getTextComponent('pairs', playerHands);

  textString += _getTextComponent('suited', playerHands, 'a');
  textString += _getTextComponent('suited', playerHands, 'k');
  textString += _getTextComponent('suited', playerHands, 'q');
  textString += _getTextComponent('suited', playerHands, 'j');
  textString += _getTextComponent('suited', playerHands, 't');
  textString += _getTextComponent('suited', playerHands, '9');
  textString += _getTextComponent('suited', playerHands, '8');
  textString += _getTextComponent('suited', playerHands, '7');
  textString += _getTextComponent('suited', playerHands, '6');
  textString += _getTextComponent('suited', playerHands, '5');
  textString += _getTextComponent('suited', playerHands, '4');
  textString += _getTextComponent('suited', playerHands, '3');

  textString += _getTextComponent('offsuit', playerHands, 'a');
  textString += _getTextComponent('offsuit', playerHands, 'k');
  textString += _getTextComponent('offsuit', playerHands, 'q');
  textString += _getTextComponent('offsuit', playerHands, 'j');
  textString += _getTextComponent('offsuit', playerHands, 't');
  textString += _getTextComponent('offsuit', playerHands, '9');
  textString += _getTextComponent('offsuit', playerHands, '8');
  textString += _getTextComponent('offsuit', playerHands, '7');
  textString += _getTextComponent('offsuit', playerHands, '6');
  textString += _getTextComponent('offsuit', playerHands, '5');
  textString += _getTextComponent('offsuit', playerHands, '4');
  textString += _getTextComponent('offsuit', playerHands, '3');

  textString = textString.slice(0, -2); // remove trailing ', '

  return textString;
}


function _getTextComponent(type, playerHands, rank) {
  var hands;
  var groups = [];
  var currentGroup = [];
  var componentString = '';

  // ex. ['jts', 'j9s', 'j8s'... ]
  if (type === 'suited') {
    hands = constants.textSelectorLists.suited[rank];
  }

  // ex. ['jto', 'j9o', 'j8o'... ]
  else if (type === 'offsuit') {
    hands = constants.textSelectorLists.offsuit[rank];
  }

  // ['aa', 'kk', 'qq'... ]
  else if (type === 'pairs') {
    hands = constants.textSelectorLists.pairs;
  }

  for (var i = 0; i < hands.length; i++) {
    if (utilities.isHandInRange(hands[i], playerHands)) {
      currentGroup.push(hands[i]);
    } else if (currentGroup.length > 0) {
      groups.push(currentGroup);
      currentGroup = [];
    }
  }

  if (currentGroup.length > 0) {
    groups.push(currentGroup);
  }

  groups.forEach(function (group) {
    if (group[0] === hands[0]) {
      if (group.length > 1) {
        componentString += group[group.length - 1] + '+, ';
      } else if (group.length === 1) {
        componentString += hands[0] + ', ';
      }
    } else if (group[group.length - 1] === hands[hands.length - 1]) {
      if (group.length > 1) {
        componentString += group[0] + '-, ';
      } else if (group.length === 1) {
        componentString += hands[hands.length - 1] + ', ';
      }
    } else {
      if (group.length === 1) {
        componentString += group[0] + ', ';
      } else {
        componentString += group[0] + '-' + group[group.length - 1] + ', ';
      }
    }
  });

  hands.forEach(function (hand) {
    var specificHands = utilities.getAllSpecificHands(hand);
    specificHands.forEach(function (specificHand) {
      if (utilities.isSpecificHandInRangeWithOtherCombosMissing(specificHand, playerHands)) {
        componentString += specificHand + ', ';
      }
    });
  });

  return componentString;
}



