import createPlayer from '@/services/player-factory';
import utilities from '@/services/utilities/utilities';
import storeHelper from '@/stores/helpers';

export default {
  state: {
    simulateButtonText: 'Simulate',
    simulateTo: 'river',
    communityCards: {
      flop1: '',
      flop2: '',
      flop3: '',
      turn: '',
      river: ''
    },
    players: [
      createPlayer(),
      createPlayer()
    ],
    initialRakePercentage: Number(localStorage.getItem('initialRakePercentage')) || 0,
    tipPercentage: Number(localStorage.getItem('tipPercentage')) || 0,
    potSize: Number(localStorage.getItem('potSize')) || 100,
    rakePercentage: Number(localStorage.getItem('rakePercentage')) || 0,
    rakeCap: Number(localStorage.getItem('rakeCap')) || 0,
    tipAmount: Number(localStorage.getItem('tipAmount')) || 0,
    handRankingsType: storeHelper.handRankingsType,
    customHandRankings: storeHelper.customHandRankings
  },
  reset: function () {
    this.state = {
      simulateButtonText: 'Simulate',
      simulateTo: 'river',
      communityCards: {
        flop1: '',
        flop2: '',
        flop3: '',
        turn: '',
        river: ''
      },
      players: [
        createPlayer(),
        createPlayer()
      ],
      initialRakePercentage: 0,
      tipPercentage: 0,
      potSize: 100,
      rakePercentage: 0,
      rakeCap: 0,
      tipAmount: 0,
      handRankingsType: storeHelper.handRankingsType,
      customHandRankings: storeHelper.customHandRankings
    };
  },

  // setters
  setSimulateButtonText: function (newText) {
    this.state.simulateButtonText = newText;
  },
  setClickContextsToNull: function () {
    this.state.players.forEach(function (player) {
      player.clickContext = null;
    });
  },
  setCommunityCard: function (communityCard, cardString) {
    this.state.communityCards[communityCard] = cardString;
  },
  setRakePercentage: function (rakePercentage) {
    localStorage.setItem('rakePercentage', rakePercentage);
    this.state.rakePercentage = rakePercentage;
  },
  setTipPercentage: function (tipPercentage) {
    localStorage.setItem('tipPercentage', tipPercentage);
    this.state.tipPercentage = tipPercentage;
  },

  // simple
  addPlayer: function () {
    var newPlayer = createPlayer();
    this.state.players.push(newPlayer);
  },
  removePlayer: function (index) {
    this.state.players.splice(index, 1);
  },

  // outsourced to storeHelper
  setHandRankingsType: function (newType) {
    storeHelper.setHandRankingsType(newType, this.state);
  },
  setCustomHandRankings: function (customHandRankingsString) {
    storeHelper.setCustomHandRankings(customHandRankingsString, this.state);
  },
  clearPlayer: function (playerIndex) {
    storeHelper.clearPlayer(this.state.players[playerIndex]);
  },
  addHandToRange: function (handString, playerIndex) {
    storeHelper.addHandToRange(handString, this.state.players[playerIndex]);
  },
  removeHandFromRange: function (handString, playerIndex) {
    storeHelper.removeHandFromRange(handString, this.state.players[playerIndex]);
  },
  addTierToRange: function (tierNumber, playerIndex) {
    storeHelper.addTierToRange(tierNumber, this.state.players[playerIndex]);
  },
  removeTierFromRange: function (tierNumber, playerIndex) {
    storeHelper.removeTierFromRange(tierNumber, this.state.players[playerIndex]);
  },
  addCategoryToRange: function (categoryName, playerIndex) {
    storeHelper.addCategoryToRange(categoryName, this.state.players[playerIndex]);
  },
  removeCategoryFromRange: function (categoryName, playerIndex) {
    storeHelper.removeCategoryFromRange(categoryName, this.state.players[playerIndex]);
  },
  handleSliderChange: function (targetPercentage, playerIndex) {
    storeHelper.handleSliderChange(targetPercentage, this.state.players[playerIndex]);
  },
  updateRangeAccordingToText: function (text, playerIndex) {
    storeHelper.updateRangeAccordingToText(text, this.state.players[playerIndex]);
  },

  // logic
  updateResults: function (playerStats, numberOfSimulations) {
    var results;
    var equity;

    for (var i = 0; i < playerStats.length; i++) {
      results = this.state.players[i].results;

      equity = ((playerStats[i].wins + (playerStats[i].ties / 2)) / numberOfSimulations) * 100;
      equity -= (equity * (this.state.rakePercentage / 100)) + (equity * (this.state.tipPercentage / 100));

      results.equityString = (equity).toFixed(2) + '%';
      results.winPercentageString = utilities.getPercentageString(playerStats[i].wins, numberOfSimulations);
      results.tiePercentageString = utilities.getPercentageString(playerStats[i].ties, numberOfSimulations);
    }
  }
};



