import constants from '@/services/constants';
import utilities from '@/services/utilities/utilities';

export {
  getHandsFromText,
  _getHandsFromRangeTextComponent,
  _getHandsFromPlusTextComponent,
  _getHandsFromMinusTextComponent
}

function getHandsFromText(text) {
  text = text.toLowerCase();
  text = text.trim();

  var textComponents = text.split(', '); // 'aa, jj-99' -> ['aa', 'jj-99']
  var handsFromText = [];
  var handsFromTextComponent;

  textComponents.forEach(function (textComponent) {
    handsFromTextComponent = [];

    // jj-
    if (textComponent[textComponent.length - 1] === '-') {
      handsFromTextComponent = _getHandsFromMinusTextComponent(textComponent)
      // ['jj', 'tt', '99'... ]
    }

    // jj+
    else if (textComponent.indexOf('+') > -1) {
      handsFromTextComponent = _getHandsFromPlusTextComponent(textComponent);
      // ['aa', 'kk', 'qq', 'jj']
    }

    // jj-99
    else if (textComponent.indexOf('-') > -1) {
      handsFromTextComponent = _getHandsFromRangeTextComponent(textComponent);
      // ['jj', 'tt', '99']
    }

    // jj
    else {
      if (textComponent.length === 4) {
        // asac --> acas
        textComponent = utilities.normalizeSpecificHandString(textComponent);
      }
      handsFromTextComponent = [textComponent];
      // ['jj']
    }

    handsFromTextComponent.forEach(function (handFromTextComponent) {
      handsFromText.push(handFromTextComponent)
    });
  });

  return handsFromText;
}

// ex. jj-99
function _getHandsFromRangeTextComponent(textString) {
  var i;
  var firstRank;
  var highRankIndex;
  var lowRankIndex;
  var handsFromRangeTextComponent = [];

  // aqs-a9s
  if (textString.indexOf('s') > -1) {
    firstRank = textString[0]; // a
    highRankIndex = constants.ranks.indexOf(textString[1]); // 2
    lowRankIndex = constants.ranks.indexOf(textString[5]); // 5

    // q..9
    for (i = highRankIndex; i <= lowRankIndex; i++) {
      handsFromRangeTextComponent.push(firstRank + constants.ranks[i] + 's');
    }
  }

  // aqo-a9o
  else if (textString.indexOf('o') > -1) {
    firstRank = textString[0]; // a
    highRankIndex = constants.ranks.indexOf(textString[1]); // 2
    lowRankIndex = constants.ranks.indexOf(textString[5]); // 5

    // q..9
    for (i = highRankIndex; i <= lowRankIndex; i++) {
      handsFromRangeTextComponent.push(firstRank + constants.ranks[i] + 'o');
    }
  }

  // qq-99
  else {
    highRankIndex = constants.ranks.indexOf(textString[0]); // 2
    lowRankIndex = constants.ranks.indexOf(textString[3]); // 5

    // q..9
    for (i = highRankIndex; i <= lowRankIndex; i++) {
      handsFromRangeTextComponent.push(constants.ranks[i] + constants.ranks[i]);
    }
  }

  return handsFromRangeTextComponent;
}

// ex. jj+
function _getHandsFromPlusTextComponent(textString) {
  var i;
  var firstRank;
  var highRankIndex;
  var lowRankIndex;
  var handsFromPlusTextComponent = [];

  // ajs+
  if (textString.indexOf('s') > -1) {
    firstRank = textString[0]; // a
    highRankIndex = constants.ranks.indexOf(textString[0]) + 1; // 1
    lowRankIndex = constants.ranks.indexOf(textString[1]); // 3

    // k..j
    for (i = highRankIndex; i <= lowRankIndex; i++) {
      handsFromPlusTextComponent.push(firstRank + constants.ranks[i] + 's');
    }
  }

  // ajo+
  else if (textString.indexOf('o') > -1) {
    firstRank = textString[0]; // a
    highRankIndex = constants.ranks.indexOf(textString[0]) + 1; // 1
    lowRankIndex = constants.ranks.indexOf(textString[1]); // 3

    // k..j
    for (i = highRankIndex; i <= lowRankIndex; i++) {
      handsFromPlusTextComponent.push(firstRank + constants.ranks[i] + 'o');
    }
  }

  // jj+
  else {
    lowRankIndex = constants.ranks.indexOf(textString[0]); // 3

    // a..j
    for (i = 0; i <= lowRankIndex; i++) {
      handsFromPlusTextComponent.push(constants.ranks[i] + constants.ranks[i]);
    }
  }

  return handsFromPlusTextComponent;
}

// ex. jj-
function _getHandsFromMinusTextComponent(textString) {
  var i;
  var firstRank;
  var highRankIndex;
  var handsFromMinusTextComponent = [];

  // 84s-
  if (textString.indexOf('s') > -1) {
    firstRank = textString[0]; // 8
    highRankIndex = constants.ranks.indexOf(textString[1]); // 10

    // 4..2
    for (i = highRankIndex; i < 13; i++) {
      handsFromMinusTextComponent.push(firstRank + constants.ranks[i] + 's');
    }
  }

  // 84o-
  else if (textString.indexOf('o') > -1) {
    firstRank = textString[0]; // 8
    highRankIndex = constants.ranks.indexOf(textString[1]); // 10

    // 4..2
    for (i = highRankIndex; i < 13; i++) {
      handsFromMinusTextComponent.push(firstRank + constants.ranks[i] + 'o');
    }
  }

  // 44-
  else {
    highRankIndex = constants.ranks.indexOf(textString[0]); // 10

    // 4..2
    for (i = highRankIndex; i < 13; i++) {
      handsFromMinusTextComponent.push(constants.ranks[i] + constants.ranks[i]);
    }
  }

  return handsFromMinusTextComponent;
};



