<template>
  <div>
    <div class="input-group">
      <input
        ref="text-input"
        type="text"
        class="form-control"
        autocomplete="false"
        spellcheck="false"
        v-bind:value="getTextFromHands()"
        v-on:keyup.enter="updateRangeAccordingToText()"
      />
      <div class="input-group-btn">
        <button
          class="btn btn-default"
          data-toggle="modal"
          data-target="#myModal"
        >
          ?
        </button>
        <button
          class="btn btn-default"
          v-on:click="updateRangeAccordingToText()"
        >
          Update
        </button>
      </div>
    </div>

    <div class="modal fade" id="myModal">
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <button type="button" class="close" data-dismiss="modal">
              <span>&times;</span>
            </button>
            <h4 class="modal-title">Text Selector Rules</h4>
          </div>
          <div class="modal-body">
            <table class="table">
              <thead>
                <tr>
                  <th>Correct</th>
                  <th>Incorrect</th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td>AA, 44, 76s, KJo</td>
                  <td>AA,44,76s,KJo</td>
                </tr>
                <tr>
                  <td>44, KJo, 76s, AA (order doesn't matter)</td>
                  <td></td>
                </tr>
                <tr>
                  <td>kk, t7s, kqo (case insensitive)</td>
                  <td></td>
                </tr>
                <tr>
                  <td>AhKh (specific suits)</td>
                  <td></td>
                </tr>
                <tr>
                  <td>TT</td>
                  <td>1010</td>
                </tr>
                <tr>
                  <td>76s</td>
                  <td>67s</td>
                </tr>
                <tr>
                  <td>JJ+ (shortcut for JJ, QQ, KK, AA)</td>
                  <td>AA+ (can't go any higher than ace)</td>
                </tr>
                <tr>
                  <td>JJ, QQ, KK, AA (shortcuts aren't required)</td>
                  <td></td>
                </tr>
                <tr>
                  <td>44- (shortcut for 44, 33, 22)</td>
                  <td>22- (can't go any lower than two)</td>
                </tr>
                <tr>
                  <td>K8s+ (shortcut for K8s, K9s, KTs, KJs, KQs)</td>
                  <td>KQs+ (can't go up to KKs and KAs)</td>
                </tr>
                <tr>
                  <td>99-55</td>
                  <td>KJs-K7o, KJs-87s</td>
                </tr>
                <tr>
                  <td></td>
                  <td>55-99, K7s-KJs</td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getTextFromHands } from '@/services/text/get-text-from-hands';

export default {
  name: 'text-selector',
  props: ['hands'],
  methods: {
    getTextFromHands: function () {
      var textString = getTextFromHands(this.hands);

      textString = textString.replace(/a/g, "A");
      textString = textString.replace(/k/g, "K");
      textString = textString.replace(/q/g, "Q");
      textString = textString.replace(/j/g, "J");
      textString = textString.replace(/t/g, "T");

      return textString;
    },
    updateRangeAccordingToText: function () {
      var text = this.$refs['text-input'].value
      this.$emit('updateRangeAccordingToText', text);
    }
  }
};
</script>

<style lang="scss" scoped>
  .input-group {
    width: 100%;

    input[type="text"] {
      width: 100%;
      cursor: auto;
    }
  }

  @media (max-width: 436px) {
    .input-group {
      margin-top: 10px;
    }
  }
</style>



