<template>
  <div id="app" v-on:mouseup="setClickContextToNull()">
    <navbar></navbar>
    <div class="container">
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
  import Navbar from '@/components/navbar'
  import equityCalculatorStore from '@/stores/equity-calculator';
  import outcomeAnalyzerStore from '@/stores/outcome-analyzer';

  export default {
    name: 'app',
    components: {
      navbar: Navbar
    },
    methods: {
      setClickContextToNull: function () {
        equityCalculatorStore.setClickContextsToNull();
        outcomeAnalyzerStore.setClickContextToNull();
      }
    }
  }
</script>

<style lang="scss">
  $icon-font-path: "~bootstrap-sass/assets/fonts/bootstrap/";
  $font-size-base: 12px;
  $container-large-desktop: 1240px;
  @import "node_modules/bootstrap-sass/assets/stylesheets/bootstrap";
  @import "src/assets/app";
</style>



