export default validateBeforeSimulation;

function validateBeforeSimulation (playerRanges, communityCards, simulateTo) {
  var validatePlayerRangesNotEmptyResult;
  var validateCommunityCardsResult;
  var validateSimulateToResult;

  validatePlayerRangesNotEmptyResult = _validatePlayerRangesNotEmpty(playerRanges);
  if (validatePlayerRangesNotEmptyResult.error) {
    return {
      error: true,
      errorMessage: validatePlayerRangesNotEmptyResult.errorMessage
    };
  }

  validateCommunityCardsResult = _validateCommunityCards(communityCards);
  if (validateCommunityCardsResult.error) {
    return {
      error: true,
      errorMessage: validateCommunityCardsResult.errorMessage
    };
  }

  validateSimulateToResult = _validateSimulateTo(simulateTo, communityCards);
  if (validateSimulateToResult.error) {
    return {
      error: true,
      errorMessage: validateSimulateToResult.errorMessage
    };
  }

  return {
    error: false
  };
}

function _validatePlayerRangesNotEmpty (playerRanges) {
  var error = false;
  var errorMessage = '';

  for (var i = 0; i < playerRanges.length; i++) {
    if (playerRanges[i].length === 0) {
      error = true;
      errorMessage = 'One or more player range is empty.';
    }
  }

  return {
    error: error,
    errorMessage: errorMessage
  };
}
function _validateCommunityCards (communityCards, simulateTo) {
  var error = false;
  var errorMessage = '';

  if (communityCards.river && (!communityCards.turn || !communityCards.flop3 || !communityCards.flop2 || !communityCards.flop1)) {
    error = true;
    errorMessage = 'You can\'t set the river without setting the flop and turn.';
  }

  if (communityCards.turn && (!communityCards.flop3 || !communityCards.flop2 || !communityCards.flop1)) {
    error = true;
    errorMessage = 'You can\'t set the turn without setting the flop.';
  }

  if (communityCards.flop3 && (!communityCards.flop2 || !communityCards.flop1)) {
    error = true;
    errorMessage = 'You can\'t set the third community card without setting the first two.';
  }

  if (communityCards.flop2 && !communityCards.flop1) {
    error = true;
    errorMessage = 'You can\'t set the second community card without setting the first.';
  }

  return {
    error: error,
    errorMessage: errorMessage
  };
}

function _validateSimulateTo (simulateTo, communityCards) {
  var error = false;
  var errorMessage = '';

  if (simulateTo === 'flop') {
    if (communityCards.turn && communityCards.river) {
      error = true;
      errorMessage = 'You are only simulating to the flop, but you manually set the turn and river cards.';
    }

    if (communityCards.turn) {
      error = true;
      errorMessage = 'You are only simulating to the flop, but you manually set the turn card.';
    }

    if (communityCards.river) {
      error = true;
      errorMessage = 'You are only simulating to the flop, but you manually set the river card.';
    }

    if (communityCards.flop1 && communityCards.flop2 && communityCards.flop3) {
      error = true;
      errorMessage = 'You are trying to simulate to the flop, but have already set all three flop cards.';
    }
  }

  if (simulateTo === 'turn') {
    if (communityCards.river) {
      error = true;
      errorMessage = 'You are only simulating to the turn, but you manually set the river card.';
    }

    if (communityCards.flop1 && communityCards.flop2 && communityCards.flop3 && communityCards.turn) {
      error = true;
      errorMessage = 'You are trying to simulate to the turn, but have already set the first four community cards.';
    }
  }

  if (simulateTo === 'river') {
    if (communityCards.flop1 && communityCards.flop2 && communityCards.flop3 && communityCards.turn && communityCards.river) {
      error = true;
      errorMessage = 'You are trying to simulate to the river, but have already set all five community cards.';
    }
  }

  return {
    error: error,
    errorMessage: errorMessage
  };
}



