<template>
  <form>
    <section class="form-group">
      <label>
        Pot size ($):
        <input type="number" class="form-control" v-model="potSize" min="0">
      </label>
    </section>
    <section class="form-group">
      <label>
        Rake percentage <a href="https://www.pokerschoolonline.com/articles/Beginning-Poker-11-Rake-Considerations">(usually 5-10%)</a>:
        <input type="number" class="form-control" v-model="rakePercentage" min="0" max="100">
      </label>
    </section>
    <section class="form-group">
      <label>
        Rake cap <a href="https://www.pokernews.com/strategy/las-vegas-poker-cash-games-rake-comps-27539.htm">(usually $3-5)</a>:
        <input type="number" class="form-control" v-model="rakeCap" min="0">
      </label>
    </section>
    <section class="form-group">
      <label>
        Tip amount ($):
        <input type="number" class="form-control" v-model="tipAmount" min="0">
      </label>
    </section>
    <button type="button" class="btn btn-default" v-on:click="resetRakeAndTip()">Reset</button>
    <button type="button" class="btn btn-primary" v-on:click="saveRakeAndTip()">Save</button>
  </form>
</template>

<script>
import equityCalculatorStore from '@/stores/equity-calculator';

export default {
  name: 'rake-and-tip',
  data: function () {
    return {
      potSize: equityCalculatorStore.state.potSize,
      rakePercentage: equityCalculatorStore.state.initialRakePercentage,
      rakeCap: equityCalculatorStore.state.rakeCap,
      tipAmount: equityCalculatorStore.state.tipAmount,
    };
  },
  methods: {
    saveRakeAndTip: function () {
      var potSize = Number(this.potSize);
      var initialRakePercentage = Number(this.rakePercentage);
      var rakeCap = Number(this.rakeCap);
      var tipAmount = Number(this.tipAmount);
      var rakePercentage = Math.min(
        rakeCap,
        (initialRakePercentage / 100) * potSize
      );
      var tipPercentage = (tipAmount / potSize) * 100;

      // save to equityCalculatorStore
      equityCalculatorStore.state.potSize = potSize;
      equityCalculatorStore.state.initialRakePercentage = initialRakePercentage;
      equityCalculatorStore.state.rakeCap = rakeCap;
      equityCalculatorStore.state.tipAmount = tipAmount;

      // save to localstorage
      localStorage.setItem('potSize', potSize);
      localStorage.setItem('initialRakePercentage', initialRakePercentage);
      localStorage.setItem('rakeCap', rakeCap);
      localStorage.setItem('tipAmount', tipAmount);

      equityCalculatorStore.setRakePercentage(rakePercentage);
      equityCalculatorStore.setTipPercentage(tipPercentage);
      alert('Saved!');
    },
    resetRakeAndTip: function () {
      this.potSize = 100,
      this.rakePercentage = 0;
      this.rakeCap = 0;
      this.tipAmount = 0;
      this.saveRakeAndTip();
    }
  }
};
</script>

<style lang="scss" scoped>
</style>



