import { HorizontalBar, mixins } from 'vue-chartjs';
var reactiveProp = mixins.reactiveProp;

export default HorizontalBar.extend({
  mixins: [reactiveProp],
  props: ['options'],
  mounted: function() {
    this.renderChart(this.chartData, this.options)
  }
});



