import validateBeforeSimulation from '@/services/simulator/validate-before-simulation';
import utilities from '@/services/utilities/utilities';

export default simulate;
export { _getRange }

function simulate (store, players, communityCards, simulateTo, numberOfSimulations) {
  var requestBody = {
    playerRanges: [],
    numberOfSimulations: numberOfSimulations,
    presetCommunityCards: _getCommunityCardsArray(communityCards),
    simulateTo: simulateTo
  };
  var validateResult;

  // set player ranges
  players.forEach(function (player) {
    var range = _getRange(player.hands);
    requestBody.playerRanges.push(range);
  });

  validateResult = validateBeforeSimulation(requestBody.playerRanges, communityCards, simulateTo);
  if (validateResult.error) {
    alert(validateResult.errorMessage);
    return;
  }

  store.setSimulateButtonText('Simulating...');

  return $.ajax({
    url: process.env.API_SERVER_URL + '/simulate',
    method: 'POST',
    contentType: 'application/json',
    data: JSON.stringify(requestBody)
  })
    .done(function (playerStats) {
      store.setSimulateButtonText('Done!');
      store.updateResults(playerStats, numberOfSimulations);

      setTimeout(function () {
        store.setSimulateButtonText('Simulate');
      }, 1000);
    })
  ;
}

function _getCommunityCardsArray (communityCardsObj) {
  var arr = [];

  if (communityCardsObj.flop1) {
    arr.push(communityCardsObj.flop1);
  }

  if (communityCardsObj.flop2) {
    arr.push(communityCardsObj.flop2);
  }

  if (communityCardsObj.flop3) {
    arr.push(communityCardsObj.flop3);
  }

  if (communityCardsObj.turn) {
    arr.push(communityCardsObj.turn);
  }

  if (communityCardsObj.river) {
    arr.push(communityCardsObj.river);
  }

  return arr;
}

function _getRange (playerHands) {
  var range = [];
  var specificHands;

  playerHands.forEach(function (playerHand) {
    if (playerHand.inRange === true) {
      specificHands = utilities.getAllSpecificHands(playerHand.string);
      specificHands.forEach(function (specificHand) {
        range.push(specificHand);
      });
    }

    else if (playerHand.inRange === 'partial') {
      for (var combo in playerHand.combos) {
        if (playerHand.combos.hasOwnProperty(combo)) {
          if (playerHand.combos[combo]) {
            range.push(playerHand.string[0] + combo[0] + playerHand.string[1] + combo[1]);
          }
        }
      }
    }
  });

  return range;
}



