import constants from '@/services/constants';
import utilities from '@/services/utilities/utilities';

export default function () {
  var hands = [];
  var numCombos;
  var handType;
  var combos;

  constants.handStrings.forEach(function (handString) {
    handType = utilities.getHandType(handString);

    if (handType === 'pair') {
      numCombos = 6;
      combos = {
        cs: false,
        ch: false,
        cd: false,
        sh: false,
        sd: false,
        hd: false
      };
    } else if (handType === 'suited') {
      numCombos = 4;
      combos = {
        cc: false,
        ss: false,
        hh: false,
        dd: false
      };
    } else if (handType === 'offsuit') {
      numCombos = 12;
      combos = {
        cs: false,
        ch: false,
        cd: false,
        sc: false,
        sh: false,
        sd: false,
        hc: false,
        hs: false,
        hd: false,
        dc: false,
        ds: false,
        dh: false
      };
    }

    hands.push({
      string: handString,
      inRange: false,
      handType: handType,
      numCombos: numCombos,
      combos: combos
    });
  });

  return {
    hands: hands,
    numberOfSpecificHandsInRange: 0,
    clickContext: null,
    results: {
      equityString: '',
      winPercentageString: '',
      tiePercentageString: '',
      bestHandProbabilities: {
        highCard: null,
        pair: null,
        twoPair: null,
        threeOfAKind: null,
        straight: null,
        flush: null,
        fullHouse: null,
        fourOfAKind: null,
        straightFlush: null,
        royalFlush: null
      }
    }
  };
};



