import Vue from 'vue';
import VueAnalytics from 'vue-analytics'
import Router from 'vue-router';
import Home from '@/components/home';
import EquityCalculator from '@/components/equity-calculator';
import OutcomeAnalyzer from '@/components/outcome-analyzer';
import Settings from '@/components/settings/settings';
import Guide from '@/components/guide/guide';

Vue.use(Router)

var router = new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    }, {
      path: '/equity-calculator',
      name: 'Equity Calculator',
      component: EquityCalculator
    }, {
      path: '/outcome-analyzer',
      name: 'Outcome Analyzer',
      component: OutcomeAnalyzer
    }, {
      path: '/settings',
      name: 'Settings',
      component: Settings
    }, {
      path: '/guide',
      name: 'Guide',
      component: Guide
    }
  ]
});

if (process.env.NODE_ENV === 'production') {
  Vue.use(VueAnalytics, {
    id: process.env.GOOGLE_ANALYTICS_TRACKING_ID,
    router: router
  });
}

export default router;



