<template>
  <section>
    <header class="page-header">
      <h1>Premium Poker Tools (under construction 👷🏗)</h1>
    </header>

    <h2>What</h2>
    <p>Premium Poker Tools currently offers an <router-link to="/equity-calculator">equity calculator</router-link> and an <router-link to="/outcome-analyzer">outcome analyzer</router-link>.</p>
    <img src="/static/equity-calculator-and-outcome-analyzer.png">
    <p>In the future, it will offer more tools. Eventually a version will be released that I will charge for.</p>

    <h2>Under construction</h2>
    <p>"Talk to users and iterate" is what I'm aiming to do. I believe that that is how you build a good product. "Design thinking" and "having a good product" sense are useful, but they can only get you so far. Thinking you know everything about what users want is tempting, but fallible.</p>
    <p>Too many people spend six months in a garage building a product, release it, and then only make a few minor tweaks in response to user feedback. I won't be one of those people.</p>

    <h2>Specific feedback I'm looking for</h2>
    <ol>
      <li>Would you pay for this app as is?</li>
      <li>Would you pay for this app if I made certain improvements? Which ones?</li>
      <li>What features do you really want to see? What features do you only kinda want to see?</li>
      <li>If you are using a different app, what bothers you about it?</li>
      <li>Does anything about this app confuse you?</li>
      <li>Is the slider easy enough to use on mobile? Would you prefer a text input? With a +1 and -1 button? And a +5 and -5 button?</li>
      <li>What selectors (grid, tier, category, slider, text) do you actually use on mobile? If there is a selector that no one uses, it would be nice to get rid of it in order to clear up space.</li>
    </ol>
    <p>If you live in Vegas, I would love to buy you a meal/drink/coffee/whatever and test the app out in person with you. If you're interested, email me at <a href="mailto:azerner3@gmail.com">azerner3@gmail.com</a>.</p>

    <h2>Potential features to add</h2>
    <p><strong>1. Speed.</strong> I can make the simulations run a lot faster. Right now you have to make a request over the internet to run a simulation. Requests over the internet are slow. Instead, I could tweak the code so that the simulations run on your browser.</p>
    <p><strong>2. Showing draws in the outcome analyzer.</strong> Right now it shows how often you get different <em>hands</em>, like pair, two pair, three of a kind, etc. I'd like to also show how often you get a flush draw, straight draw, gut shot, etc. I'd also like to be more precise and specify overpair, top pair, middle pair and weak pair.</p>
    <p><strong>3. Specifying the weight of a hands.</strong> For example, suppose you think your opponent would 4-bet you with a range of AA, KK, QQ, and AKs for value, and A7o, A6o, A5o, A4o, A3o and A2o as a bluff. Here your opponent is raising as a bluff 50% more often than he is raising for value. Perhaps you think he'd raise for value more often. It would be nice to say that A7o- is in his range, but that they only appear half as frequently as AA, KK, QQ, and AKs. A hand weight feature would be nice.</p>
    <p>Still, this is something I'd like to talk through with users. If your current software offers it, do you use it a lot? I suspect that it would be best to include the feature, but to not crowd the UI with controls for the feature when you're not using it. Perhaps have a Settings menu where you could enable it when you need it rather than displaying it by default.</p>
    <p><strong>4. Offline support.</strong> Ie. being able to use the app with a web brower, but without having connection to the internet. I know that may seem impossible, but it's not. Once I make the speed improvements, this feature won't take too much more work.</p>
    <p><strong>5. The ability to easily share results in poker forums.</strong> Much better than making the user figure out how to take a snapshot of their screen, save it, and then figure out how to upload the image on the poker forum.</p>
    <p><strong>6. "How often will my range flop X% equity?"</strong> It would be nice to have a graph displaying answers to these questions.</p>
    <p><strong>7. A selector to preset board texture.</strong> Instead of selecting specific community cards, maybe you want to preset the board to have two cards of the same suit.</p>
    <p><strong>8. A native mobile app.</strong> This of course would take time to build. Whether I do so depends on how big of a demand there is for it. Until then, you can use the web browser on your phone to use Premium Poker Tools.</p>

    <h2>Competitors</h2>
    <p>Here are some gripes I have with the design of currently existing apps that contributed to motivating me to build my own.</p>
    <h3>1. Flopzilla (only available for Windows)</h3>
    <p>I think the outcome analyzer on Flopzilla is pretty solid. It looks like this:</p>
    <img src="http://www.flopzilla.com/tmp_files/manual/screenshot1.png">
    <p>Here is the full range selector:</p>
    <img src="http://www.flopzilla.com/tmp_files/manual/matrix.png">
    <p>I think the colors are unintuitive. I prefer green for suited and red for offsuit because of the good-bad connotation. Yellow-red doesn't make me think "good-bad". I also prefer a neutral color like gray for the pocket pairs.</p>
    <p>I wish the range selector had check boxes for tiers and categories. I find myself using those check boxes a lot. I also find that looking at which check boxes are checked is an easy way to understand a hand range at a glance.</p>
    <img src="/static/tier-and-category-selectors.png" width="500">
    <p>I also find it nice to have the text summary of the range right next to the grid.</p>
    <img src="/static/text-selector.png" width="500">
    <p>The equity calculator for Flopzilla is actually a separate app called HoldEq, which I think is unnecessarily confusing.</p>
    <p>The default view of the ranges and results looks like it's from the 1990s, but usability is more important than aesthetics.</p>
    <img src="http://www.flopzilla.com/tmp_files/holdeq2291115.png">
    <p>Usability-wise, I think it is very important to have the grids <strong>side by side</strong>. Getting them side by side is <em>possible</em> with HoldEq, but not totally <em>intuitive</em>. You have to open various windows, and then "connect" them.</p>
    <img src="/static/holdeq-side-by-side.png" width="500">
    <p>With Premium Poker Tools, the equity calculator automatically places the player ranges side by side. It is intuitive. You don't have to spend time playing with window sizes, and you don't have to do any "connecting".</p>
    <img src="/static/side-by-side-grids.png" width="500">
    <p>Here they are side by side <em>by side</em>!</p>
    <img src="/static/side-by-side-by-side-grids.png" width="500">

    <h3>2. PokerCruncher (only availabile for Macs and iOS)</h3>
    <img src="/static/poker-cruncher-confusing-ui.png" width="500">
    <p>I think the usability and design of PokerCruncher is "meh" at best.</p>
    <p>The selected hands don't have a color that helps you tell whether it's suited or not (eg. green → dark green):</p>
    <img src="/static/poker-cruncher-no-color.png">
    <p>Selecting and removing hands requires unintuitive combinations of the shift and control keys:</p>
    <iframe width="560" height="315" src="https://www.youtube.com/embed/j98QFH_Dc9A?rel=0&amp;start=305" frameborder="0" allowfullscreen></iframe>
    <p>In my opinion, the biggest drawback is that you can't see the grid selector for two or more players side by side like you can with Premium Poker Tools and HoldEq:</p>
    <img src="/static/side-by-side-ppt-and-holdeq.png" width="500">
    <p>To me, that's a dealbreaker.</p>

    <h3>3. Pro Poker Tools (available for Mac and Windows users)</h3>
    <p>I think the design here is poor.</p>
    <p>The UI is similar to HoldEq in that you have to click to open the grid selector.</p>
    <img src="/static/propokertools.png" width="500">
    <p>However, you can't open up two grid selectors at once so that you can see them side by side like you can with HoldEq:</p>
    <img src="/static/holdeq-side-by-side.png" width="500">
    <p>Again, that alone is a dealbreaker to me.</p>
    <p>You also can't click and drag to select hands in the "layered" grid selector. You have to tab over to the <em>basic</em> grid selector if you want to do that.</p>
    <p>On top of that, the grid isn't very responsive. Watch how long it takes for the grid cells to clear:</p>
    <iframe width="560" height="315" src="https://www.youtube.com/embed/sLQ1G4M08Zs?rel=0&amp;showinfo=0" frameborder="0" allowfullscreen></iframe>
    <p>Compare that to Premium Poker Tools:</p>
    <iframe width="560" height="315" src="https://www.youtube.com/embed/kOxK4AV5sZQ?rel=0&amp;showinfo=0" frameborder="0" allowfullscreen></iframe>
    <p>If you want to see how often a range makes a pair, two pair, thre of a kind etc., you have to awkwardly "ask a bunch of questions":</p>
    <iframe width="560" height="315" src="https://www.youtube.com/embed/qaFiqYmMqEk?rel=0&amp;showinfo=0" frameborder="0" allowfullscreen></iframe>
    <p>What if I wanted to see a breakdown of all 10 possible hands? I don't want to sit there typing out all of those questions.</p>
    <p>And the view of the answers is clunky:</p>
    <img src="/static/propokertools-answers.png" width="500">
    <p>A simple horizontal bar chart would be nice:</p>
    <img src="/static/horizontal-bar-chart.png" width="500">
  </section>
</template>

<script>
export default {
  name: 'home'
};
</script>

<style lang="scss" scoped>
  section {
    font-size: 18px;
  }

  p, li {
    max-width: 700px;
  }

  img {
    margin-top: 10px;
    margin-bottom: 20px;
    display: block;
    max-width: 100%;
  }

  .strikethrough {
    text-decoration: line-through;
  }
</style>



