import constants from '@/services/constants';
import _ from 'lodash';

export default {
  hasDuplicates: function (a) {
    return _.uniq(a).length !== a.length;
  },
  isHandInRange: function (handString, playerHands) {
    var handIndex = constants.handStringIndexMap[handString];
    return playerHands[handIndex].inRange === true;
  },
  isSpecificHandInRangeWithOtherCombosMissing: function (specificHandString, playerHands) {
    var generalHandString = this.getGeneralHandFromSpecificHand(specificHandString);
    var generalHandIndex = constants.handStringIndexMap[generalHandString];
    var generalHandObj = playerHands[generalHandIndex];
    var suits = specificHandString[1] + specificHandString[3];

    if (generalHandObj.inRange === 'partial' && generalHandObj.combos[suits]) {
      return true;
    }

    return false;
  },
  areHandsInRange: function (handStrings, playerHands) {
    for (var i = 0; i < handStrings.length; i++) {
      if (!this.isHandInRange(handStrings[i], playerHands)) {
        return false;
      }
    }

    return true;
  },
  getInRangeValue: function (handString, playerHands) {
    var handIndex = constants.handStringIndexMap[handString];
    return playerHands[handIndex].inRange;
  },
  getPercentageString: function (count, numSimulations) {
    return ((count / numSimulations) * 100).toFixed(2) + '%';
  },
  getHandType: function (handString) {
    var suit;
    var rank1;
    var rank2;
    var suit1;
    var suit2;

    if (handString.length === 4) {
      rank1 = handString[0];
      rank2 = handString[2];
      suit1 = handString[1];
      suit2 = handString[3];

      if (rank1 === rank2) {
        return 'pair';
      } else if (suit1 === suit2) {
        return 'suited';
      } else {
        return 'offsuit';
      }
    }

    else {
      suit = handString[2];

      if (suit === 's') {
        return 'suited';
      } else if (suit === 'o') {
        return 'offsuit';
      } else {
        return 'pair';
      }
    }

  },
  getGeneralHandFromSpecificHand: function (specificHandString) {
    var rank1 = specificHandString[0];
    var rank2 = specificHandString[2];
    var suit1 = specificHandString[1];
    var suit2 = specificHandString[3];
    var suitedLetter;

    if (rank1 === rank2) {
      suitedLetter = '';
    } else if (suit1 === suit2) {
      suitedLetter = 's';
    } else {
      suitedLetter = 'o';
    }

    return rank1 + rank2 + suitedLetter;
  },
  getAllSpecificHands: function (generalHand) {
    var specificHands = [];
    var handType = this.getHandType(generalHand);

    if (handType === 'pair') {
      specificHands.push(generalHand[0] + 'c' + generalHand [1] + 's');
      specificHands.push(generalHand[0] + 'c' + generalHand [1] + 'h');
      specificHands.push(generalHand[0] + 'c' + generalHand [1] + 'd');
      specificHands.push(generalHand[0] + 's' + generalHand [1] + 'h');
      specificHands.push(generalHand[0] + 's' + generalHand [1] + 'd');
      specificHands.push(generalHand[0] + 'h' + generalHand [1] + 'd');
    }

    else if (handType === 'suited') {
      specificHands.push(generalHand[0] + 'c' + generalHand[1] + 'c');
      specificHands.push(generalHand[0] + 's' + generalHand[1] + 's');
      specificHands.push(generalHand[0] + 'h' + generalHand[1] + 'h');
      specificHands.push(generalHand[0] + 'd' + generalHand[1] + 'd');
    }

    else if (handType === 'offsuit') {
      specificHands.push(generalHand[0] + 'c' + generalHand [1] + 's');
      specificHands.push(generalHand[0] + 'c' + generalHand [1] + 'h');
      specificHands.push(generalHand[0] + 'c' + generalHand [1] + 'd');
      specificHands.push(generalHand[0] + 's' + generalHand [1] + 'c');
      specificHands.push(generalHand[0] + 's' + generalHand [1] + 'h');
      specificHands.push(generalHand[0] + 's' + generalHand [1] + 'd');
      specificHands.push(generalHand[0] + 'h' + generalHand [1] + 'c');
      specificHands.push(generalHand[0] + 'h' + generalHand [1] + 's');
      specificHands.push(generalHand[0] + 'h' + generalHand [1] + 'd');
      specificHands.push(generalHand[0] + 'd' + generalHand [1] + 'c');
      specificHands.push(generalHand[0] + 'd' + generalHand [1] + 's');
      specificHands.push(generalHand[0] + 'd' + generalHand [1] + 'h');
    }

    return specificHands;
  },
  normalizeSpecificHandString: function (specificHandString) {
    if (this.getHandType(specificHandString) !== 'pair') {
      return specificHandString;
    }

    var rank1 = specificHandString[0];
    var rank2 = specificHandString[2];
    var suit1 = specificHandString[1];
    var suit2 = specificHandString[3];

    var suitOrder = ['c', 's', 'h', 'd'];
    var suitOneIndex = suitOrder.indexOf(suit1);
    var suitTwoIndex = suitOrder.indexOf(suit2);

    if (suitOneIndex > suitTwoIndex) {
      return rank1 + suit2 + rank2 + suit1;
    } else {
      return rank1 + suit1 + rank2 + suit2;
    }
  }
};



