<template>
  <div>
    <span class="result">
      <div class="result-label">Equity:</div>
      <div class="result-value">{{ results.equityString || '0.00%' }}</div>
    </span>

    <span class="result">
      <div class="result-label">Win:</div>
      <div class="result-value">{{ results.winPercentageString || '0.00%' }}</div>
    </span>
    <span class="result">
      <div class="result-label">Tie:</div>
      <div class="result-value">{{ results.tiePercentageString || '0.00%' }}</div>
    </span>
    <span class="close" v-if="!playerOneOrTwo" v-on:click="removePlayer()">&times;</span>
    <button
      class="btn btn-default clear-range"
      v-on:click="clearRange()"
    >
      Clear<span> range</span>
    </button>
    <span
      class="glyphicon glyphicon-refresh"
      v-on:click="clearRange()"
    ></span>
  </div>
</template>

<script>
export default {
  name: 'player-heading',
  props: ['results', 'playerOneOrTwo'],
  methods: {
    removePlayer: function () {
      this.$emit('removePlayer');
    },
    clearRange: function () {
      this.$emit('clearRange');
    }
  }
};
</script>

<style lang="scss" scoped>
  .result {
    display: inline-block;
    margin-right: 15px;

    .result-label {
      font-size: 7px;
    }

    .result-value {
      font-size: 16px;
    }
  }

  .clear-range {
    float: right;
  }

  .close {
    position: relative;
    top: 5px;
    float: right;
    margin-left: 15px;
  }

  .glyphicon {
    display: none;
  }

  @media (max-width: 360px) {
    .clear-range span {
      display: none;
    }
  }

  @media (max-width: 320px) {
    .clear-range {
      display: none;
    }

    .glyphicon {
      display: inline;
      float: right;
      position: relative;
      top: 10px;
      color: #000;
      opacity: 0.2;
      cursor: pointer;
    }
  }
</style>



