<template>
  <section class="outcome-analyzer">
    <section>
      <section class="player panel panel-default">
        <section class="panel-heading clearfix">
          <h3 class="panel-title">Your range</h3>
          <button
            class="btn btn-default clear-range"
            v-on:click="clearRange()"
          >
            Clear range
          </button>
        </section>
        <section class="panel-body">
          <section class="left-section clearfix">
            <grid-selector
              v-bind:playerState="state.player"
              v-on:addHandToRange="addHandToRange($event)"
              v-on:removeHandFromRange="removeHandFromRange($event)"
            ></grid-selector>
            <slider-selector
              v-bind:playerState="state.player"
              v-on:handleSliderChange="handleSliderChange($event)"
            ></slider-selector>
            <text-selector
              v-bind:hands="state.player.hands"
              v-on:updateRangeAccordingToText="updateRangeAccordingToText($event)"
            ></text-selector>
          </section>
          <section class="right-section clearfix">
            <tier-selector
              v-bind:hands="state.player.hands"
              v-bind:numPlayers="1"
              v-on:addTierToRange="addTierToRange($event)"
              v-on:removeTierFromRange="removeTierFromRange($event)"
            ></tier-selector>
            <category-selector
              v-bind:hands="state.player.hands"
              v-bind:numPlayers="1"
              v-on:addCategoryToRange="addCategoryToRange($event)"
              v-on:removeCategoryFromRange="removeCategoryFromRange($event)"
            ></category-selector>
          </section>
        </section>
      </section>
      <section class="results">
        <outcome-analyzer-results
          v-bind:chart-data="chartData"
          v-bind:options="chartOptions"
        ></outcome-analyzer-results>
      </section>
    </section>
    <section class="bottom">
      <community-card-selector
        v-on:setCommunityCard="setCommunityCard($event)"
        v-bind:communityCards="state.communityCards"
      ></community-card-selector>
      <section class="simulate-to non-mobile">
        Simulate to:
        <section class="btn-group">
          <button
            class="btn btn-default"
            v-bind:class="{ 'active': state.simulateTo === 'flop' }"
            v-on:click="state.simulateTo = 'flop'"
          >
            Flop
          </button>
          <button
            class="btn btn-default"
            v-bind:class="{ 'active': state.simulateTo === 'turn' }"
            v-on:click="state.simulateTo = 'turn'"
          >
            Turn
          </button>
          <button
            class="btn btn-default"
            v-bind:class="{ 'active': state.simulateTo === 'river' }"
            v-on:click="state.simulateTo = 'river'"
          >
            River
          </button>
        </section>
      </section>
      <section class="simulate-to mobile">
        <p>Simulate to:</p>
        <section class="btn-group btn-group-justified">
          <div class="btn-group">
            <button
              class="btn btn-default"
              v-bind:class="{ 'active': state.simulateTo === 'flop' }"
              v-on:click="state.simulateTo = 'flop'"
            >
              Flop
            </button>
          </div>
          <div class="btn-group">
            <button
              class="btn btn-default"
              v-bind:class="{ 'active': state.simulateTo === 'turn' }"
              v-on:click="state.simulateTo = 'turn'"
            >
              Turn
            </button>
          </div>
          <div class="btn-group">
            <button
              class="btn btn-default"
              v-bind:class="{ 'active': state.simulateTo === 'river' }"
              v-on:click="state.simulateTo = 'river'"
            >
              River
            </button>
          </div>
        </section>
      </section>
      <simulate-button
        v-on:simulate="simulate()"
        v-bind:text="state.simulateButtonText"
        v-bind:numPlayers="1"
      ></simulate-button>
    </section>
  </section>
</template>

<script>
import GridSelector from '@/components/player/grid-selector';
import SliderSelector from '@/components/player/slider-selector';
import TextSelector from '@/components/player/text-selector';
import TierSelector from '@/components/player/tier-selector';
import CategorySelector from '@/components/player/category-selector';
import CommunityCardSelector from '@/components/community-card-selector';
import SimulateButton from '@/components/simulate-button';
import OutcomeAnalyzerResults from '@/components/outcome-analyzer-results.js';
import outcomeAnalyzerStore from '@/stores/outcome-analyzer';
import simulate from '@/services/simulator/simulate';

export default {
  name: 'outcome-analyzer',
  components: {
    'grid-selector': GridSelector,
    'slider-selector': SliderSelector,
    'text-selector': TextSelector,
    'tier-selector': TierSelector,
    'category-selector': CategorySelector,
    'community-card-selector': CommunityCardSelector,
    'outcome-analyzer-results': OutcomeAnalyzerResults,
    'simulate-button': SimulateButton
  },
  data: function () {
    return {
      state: outcomeAnalyzerStore.state,
      chartData: null,
      chartOptions: {
        legend: {
          display: false
        },
        scales: {
          xAxes: [{
            ticks: {
              display: true,
              min: 0,
              stepSize: 5,
              callback: function (value) {
                return value + '%';
              }
            },
            gridLines: {
              display: true
            }
          }]
        },
        tooltips: {
          callbacks: {
            label: function (obj) {
              return obj.xLabel + '%';
            }
          }
        }
      }
    };
  },
  mounted: function () {
    this.fillChartData();
  },
  methods: {
    addHandToRange: function (hand) {
      outcomeAnalyzerStore.addHandToRange(hand);
    },
    removeHandFromRange: function (hand) {
      outcomeAnalyzerStore.removeHandFromRange(hand);
    },
    addTierToRange: function (tierNumber) {
      outcomeAnalyzerStore.addTierToRange(tierNumber);
    },
    removeTierFromRange: function (tierNumber) {
      outcomeAnalyzerStore.removeTierFromRange(tierNumber);
    },
    addCategoryToRange: function (categoryName) {
      outcomeAnalyzerStore.addCategoryToRange(categoryName);
    },
    removeCategoryFromRange: function (categoryName) {
      outcomeAnalyzerStore.removeCategoryFromRange(categoryName);
    },
    handleSliderChange: function (targetPercentage) {
      outcomeAnalyzerStore.handleSliderChange(targetPercentage);
    },
    updateRangeAccordingToText: function (text) {
      outcomeAnalyzerStore.updateRangeAccordingToText(text);
    },
    clearRange: function () {
      outcomeAnalyzerStore.clearPlayer();
    },
    setCommunityCard: function (params) {
      outcomeAnalyzerStore.setCommunityCard(params.communityCard, params.cardString);
    },
    simulate: function () {
      var self = this;

      simulate(outcomeAnalyzerStore, [this.state.player], this.state.communityCards, this.state.simulateTo, 5000)
        .done(function () {
          self.fillChartData();
        })
      ;
    },
    fillChartData: function () {
      var bestHandProbabilities = this.state.player.results.bestHandProbabilities;

      this.chartData = {
        labels: [
          'Royal flush', 'Straight flush', 'Four of a kind', 'Full house',
          'Flush', 'Straight', 'Three of a kind', 'Two pair', 'Pair', 'High card'
        ],
        datasets: [
          {
            label: '',
            backgroundColor: '#2980b9',
            data: [
              (bestHandProbabilities.royalFlush * 100).toFixed(2),
              (bestHandProbabilities.straightFlush * 100).toFixed(2),
              (bestHandProbabilities.fourOfAKind * 100).toFixed(2),
              (bestHandProbabilities.fullHouse * 100).toFixed(2),
              (bestHandProbabilities.flush * 100).toFixed(2),
              (bestHandProbabilities.straight * 100).toFixed(2),
              (bestHandProbabilities.threeOfAKind * 100).toFixed(2),
              (bestHandProbabilities.twoPair * 100).toFixed(2),
              (bestHandProbabilities.pair * 100).toFixed(2),
              (bestHandProbabilities.highCard * 100).toFixed(2)
            ]
          }
        ]
      };
    }
  }
};
</script>

<style lang="scss" scoped>
  .player, .results {
    float: left;
  }

  .player {
    margin-right: 50px;
    width: 600px;
    max-width: 100%;

    .panel-heading {
      vertical-align: middle;

      h3 {
        float: left;
        position: relative;
        top: 10px;
      }

      .clear-range {
        float: right;
      }
    }

    .left-section, .right-section {
      float: left;
    }

    @media (min-width: 400px) {
      .left-section {
        width: 350px;
      }
    }

    .right-section {
      margin-left: 10px;
    }

    @media (max-width: 672px) {
      .right-section {
        margin-left: 0;
        margin-top: 10px;
      }
    }
  }

  @media (max-width: 1199px) {
    .player {
      margin-right: 0px;
    }
  }

  .results {
    width: 400px;
    max-width: 100%;
  }

  .bottom {
    clear: left;

    .simulate-to {
      float: left;
      margin-left: 20px;
    }
  }

  .simulate-to.mobile {
    display: none;
  }

  @media (max-width: 1199px) {
    .results {
      margin-bottom: 25px;
    }
  }

  @media (max-width: 1199px) and (min-width: 950px) {
    .outcome-analyzer {
      width: min-content;
      margin: 0 auto;
    }
  }

  @media (max-width: 1199px) and (min-width: 992px) {
    .right-section {
      margin-top: 10px !important;
      margin-left: 0 !important;
    }
  }

  @media (max-width: 661px) {
    .simulate-to.mobile {
      display: block;
      margin-left: 0;
      margin-top: 20px;
      margin-bottom: 20px;
    }

    .simulate-to.non-mobile {
      display: none;
    }
  }
</style>



