<template>
  <div>
    <div class="tier-selector non-mobile" v-bind:class="getTierSelectorClass()">
      <div v-for="i in 8" class="checkbox">
        <label>
          <input type="checkbox" v-bind:checked="isTierInRange(i)" v-on:change="addOrRemoveTier(i)">
          Tier {{ i }}
        </label>
      </div>
    </div>
    <div class="tier-selector mobile" v-bind:class="getTierSelectorClass()">
      Tiers:

      <div class="btn-group btn-group-justified">
        <div v-for="i in 4" class="btn-group">
          <button
            class="btn btn-default"
            v-bind:class="{ 'active': isTierInRange(i) }"
            v-on:click="addOrRemoveTier(i)"
          >
            {{ i }}
          </button>
        </div>
      </div>
      <br/>
      <div class="btn-group btn-group-justified">
        <div v-for="i in 4" class="btn-group">
          <button
            class="btn btn-default"
            v-bind:class="{ 'active': isTierInRange(i + 4) }"
            v-on:click="addOrRemoveTier(i + 4)"
          >
            {{ i + 4 }}
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import utilities from '@/services/utilities/utilities';
import constants from '@/services/constants';

export default {
  name: 'tier-selector',
  props: ['numPlayers', 'hands'],
  methods: {
    isTierInRange: function (tierNumber) {
      return utilities.areHandsInRange(constants.tiers[tierNumber], this.hands);
    },
    addOrRemoveTier: function (tierNumber) {
      if (this.isTierInRange(tierNumber)) {
        this.$emit('removeTierFromRange', tierNumber);
      } else {
        this.$emit('addTierToRange', tierNumber);
      }
    },
    getTierSelectorClass: function () {
      if (this.numPlayers === 1) {
        return 'one-player';
      } else if (this.numPlayers === 2 || this.numPlayers === 4) {
        return 'two-or-four-players';
      } else {
        return 'three-five-or-more-players';
      }
    }
  },
};
</script>

<style lang="scss" scoped>
  @import "src/assets/mixins";

  .mobile {
    display: none;
  }

  .checkbox:first-child {
    margin-top: 0px;
  }

  .btn-default:focus, .btn-default:hover {
    background-color: white !important;
  }

  .btn-default.active:focus, .btn-default.active:hover {
    background-color: #e6e6e6 !important;
  }

  @media (max-width: 1199px) and (min-width: 992px) {
    .mobile:not(.three-five-or-more-players) {
      display: block;
      max-width: 350px;
      width: 100%;
      margin-left: 0px;
    }

    .non-mobile:not(.three-five-or-more-players) {
      display: none;
    }
  }

  @media (min-width: 1200px) {
    .tier-selector.three-five-or-more-players.mobile {
      display: block;
      max-width: 350px;
      width: 100%;
      margin-left: 0px;
    }

    .tier-selector.three-five-or-more-players.non-mobile {
      display: none;
    }
  }

  @media (max-width: 672px) {
    .mobile {
      display: block;
      max-width: 350px;
      width: 100%;
      margin-left: 0px;
    }

    .non-mobile {
      display: none;
    }
  }

  label {
    @include noselect();

    input[type="checkbox"] {
      bottom: 2px;
    }
  }
</style>



