<template>
  <table v-bind:class="getTableClass()">
    <thead>
      <tr>
        <th></th>
        <th>Mid</th>
        <th>Small</th>
      </tr>
    </thead>
    <tbody>
      <tr>
        <td>
          <label>
            <input type="checkbox" v-bind:checked="isCategoryInRange('pocketPairs')" v-on:change="addOrRemoveCategory('pocketPairs')">
            Pocket pairs
          </label>
        </td>
        <td>
          <input type="checkbox" v-bind:checked="isCategoryInRange('mediumPocketPairs')" v-on:change="addOrRemoveCategory('mediumPocketPairs')">
        </td>
        <td>
          <input type="checkbox" v-bind:checked="isCategoryInRange('smallPocketPairs')" v-on:change="addOrRemoveCategory('smallPocketPairs')">
        </td>
      </tr>
      <tr>
        <td>
          <label>
            <input type="checkbox" v-bind:checked="isCategoryInRange('suitedConnectors')" v-on:change="addOrRemoveCategory('suitedConnectors')">
            Suited connectors
          </label>
        </td>
        <td>
          <input type="checkbox" v-bind:checked="isCategoryInRange('mediumSuitedConnectors')" v-on:change="addOrRemoveCategory('mediumSuitedConnectors')">
        </td>
        <td>
          <input type="checkbox" v-bind:checked="isCategoryInRange('smallSuitedConnectors')" v-on:change="addOrRemoveCategory('smallSuitedConnectors')">
        </td>
      </tr>
      <tr>
        <td>
          <label>
            <input type="checkbox" v-bind:checked="isCategoryInRange('suitedOneGappers')" v-on:change="addOrRemoveCategory('suitedOneGappers')">
            Suited one gappers
          </label>
        </td>
        <td>
          <input type="checkbox" v-bind:checked="isCategoryInRange('mediumSuitedOneGappers')" v-on:change="addOrRemoveCategory('mediumSuitedOneGappers')">
        </td>
        <td>
          <input type="checkbox" v-bind:checked="isCategoryInRange('smallSuitedOneGappers')" v-on:change="addOrRemoveCategory('smallSuitedOneGappers')">
        </td>
      </tr>
      <tr>
        <td>
          <label>
            <input type="checkbox" v-bind:checked="isCategoryInRange('suitedAces')" v-on:change="addOrRemoveCategory('suitedAces')">
            Suited aces
          </label>
        </td>
        <td>
          <input type="checkbox" v-bind:checked="isCategoryInRange('mediumSuitedAces')" v-on:change="addOrRemoveCategory('mediumSuitedAces')">
        </td>
        <td>
          <input type="checkbox" v-bind:checked="isCategoryInRange('smallSuitedAces')" v-on:change="addOrRemoveCategory('smallSuitedAces')">
        </td>
      </tr>
      <tr>
        <td>
          <label>
            <input type="checkbox" v-bind:checked="isCategoryInRange('suitedKings')" v-on:change="addOrRemoveCategory('suitedKings')">
            Suited kings
          </label>
        </td>
        <td>
          <input type="checkbox" v-bind:checked="isCategoryInRange('mediumSuitedKings')" v-on:change="addOrRemoveCategory('mediumSuitedKings')">
        </td>
        <td>
          <input type="checkbox" v-bind:checked="isCategoryInRange('smallSuitedKings')" v-on:change="addOrRemoveCategory('smallSuitedKings')">
        </td>
      </tr>
    </tbody>
  </table>
</template>

<script>
import constants from '@/services/constants';
import utilities from '@/services/utilities/utilities';

export default {
  name: 'category-selector',
  props: ['numPlayers', 'hands'],
  methods: {
    isCategoryInRange: function (categoryName) {
      return utilities.areHandsInRange(constants.categories[categoryName], this.hands);
    },
    addOrRemoveCategory: function (categoryName) {
      if (this.isCategoryInRange(categoryName)) {
        this.$emit('removeCategoryFromRange', categoryName);
      } else {
        this.$emit('addCategoryToRange', categoryName);
      }
    },
    getTableClass: function () {
      if (this.numPlayers === 1) {
        return 'one-player';
      } else if (this.numPlayers === 2 || this.numPlayers === 4) {
        return 'two-or-four-players';
      } else {
        return 'three-five-or-more-players';
      }
    }
  }
};
</script>

<style lang="scss" scoped>
  @import "node_modules/bootstrap-sass/assets/stylesheets/bootstrap/variables";
  @import "src/assets/mixins";

  @media (max-width: 1199px) and (min-width: 992px) {
    table:not(.three-five-or-more-players){
      margin-top: 20px;
      width: 100%;
      margin-left: 0 !important;

      td:nth-child(2) {
        width: 100px;
      }
    }
  }

  @media (min-width: 1200px) {
    table.three-five-or-more-players {
      margin-top: 20px;
      width: 100%;
      margin-left: 0 !important;

      td:nth-child(2) {
        width: 100px;
      }
    }
  }

  @media (max-width: 672px) {
    table {
      margin-top: 20px;
      width: 100%;
      margin-left: 0 !important;

      td:nth-child(2) {
        width: 100px;
      }
    }
  }

  th {
    font-weight: normal;
    font-size: 10px;
  }

  tr {
    border-bottom: 1px solid lighten($gray-base, 75%);
  }

  th, td {
    text-align: center;
    vertical-align: middle;
  }

  td:first-child {
    text-align: left;
  }

  th:not(:first-child) {
    width: 30px;
  }

  input[type="checkbox"], label {
    cursor: pointer;
  }

  label {
    font-weight: normal;

    @include noselect();

    input[type="checkbox"] {
      margin-right: 5px;
    }
  }


  @media (max-width: 436px) {
    td {
      line-height: 4.5;
    }

    label {
      margin-bottom: 0;
    }

    input[type="checkbox"] {
      vertical-align: text-bottom;
    }
  }
</style>



