<template>
  <section>
    <header class="page-header">
      <h1>Settings</h1>
    </header>
    <ul class="nav nav-tabs">
      <li class="active"><a href="#rake-and-tip" data-toggle="tab">Rake + tip</a></li>
      <li><a href="#hand-rankings" data-toggle="tab">Hand rankings</a></li>
      <li><a href="#tiers" data-toggle="tab">Tiers</a></li>
    </ul>
    <section class="tab-content">
      <section class="tab-pane active" id="rake-and-tip">
        <rake-and-tip></rake-and-tip>
      </section>
      <section class="tab-pane" id="hand-rankings">
        <hand-rankings></hand-rankings>
      </section>
      <section class="tab-pane" id="tiers">
        <tiers></tiers>
      </section>
    </section>
  </section>
</template>

<script>
import RakeAndTip from '@/components/settings/rake-and-tip';
import HandRankings from '@/components/settings/hand-rankings';
import Tiers from '@/components/settings/tiers';

export default {
  name: 'settings',
  components: {
    'rake-and-tip': RakeAndTip,
    'hand-rankings': HandRankings,
    'tiers': Tiers
  }
};
</script>

<style lang="scss" scoped>
  .tab-content {
    margin-top: 20px;
  }

  .tab-pane {
    max-width: 700px;
  }
</style>



