<template>
  <div>
    <p>No abbreviations like JJ+. You need to enter each individual hand: AA, KK, QQ, JJ.</p>
    <div class="form-group">
      <label>
        Tier 1:
      </label>
      <input type="text" class="form-control" v-model="tier1">
    </div>
    <div class="form-group">
      <label>
        Tier 2:
      </label>
      <input type="text" class="form-control" v-model="tier2">
    </div>
    <div class="form-group">
      <label>
        Tier 3:
      </label>
      <input type="text" class="form-control" v-model="tier3">
    </div>
    <div class="form-group">
      <label>
        Tier 4:
      </label>
      <input type="text" class="form-control" v-model="tier4">
    </div>
    <div class="form-group">
      <label>
        Tier 5:
      </label>
      <input type="text" class="form-control" v-model="tier5">
    </div>
    <div class="form-group">
      <label>
        Tier 6:
      </label>
      <input type="text" class="form-control" v-model="tier6">
    </div>
    <div class="form-group">
      <label>
        Tier 7:
      </label>
      <input type="text" class="form-control" v-model="tier7">
    </div>
    <div class="form-group">
      <label>
        Tier 8:
      </label>
      <input type="text" class="form-control" v-model="tier8">
    </div>
    <button type="button" class="btn btn-default" v-on:click="reset()">Reset</button>
    <button type="button" class="btn btn-primary" v-on:click="save()">Save</button>
  </div>
</template>

<script>
import constants from '@/services/constants';
import { areCustomTiersValid } from '@/services/settings-validators';

export default {
  name: 'tiers',
  data: function () {
    return {
      tier1: constants.tiers[1].join(', '),
      tier2: constants.tiers[2].join(', '),
      tier3: constants.tiers[3].join(', '),
      tier4: constants.tiers[4].join(', '),
      tier5: constants.tiers[5].join(', '),
      tier6: constants.tiers[6].join(', '),
      tier7: constants.tiers[7].join(', '),
      tier8: constants.tiers[8].join(', ')
    };
  },
  methods: {
    reset: function () {
      constants.tiers[1] = [
        'aa', 'kk', 'qq', 'jj', 'aks'
      ];
      constants.tiers[2] = [
        'tt', 'aqs', 'ajs', 'kqs', 'ako'
      ];
      constants.tiers[3] = [
        '99', 'ats', 'kjs', 'qjs', 'jts', 'aqo'
      ];
      constants.tiers[4] = [
        '88', 'kts', 'qts', 'j9s', 't9s', '98s', 'ajo', 'kqo'
      ];
      constants.tiers[5] = [
        '77', 'a9s', 'a8s', 'a7s', 'a6s', 'a5s', 'a4s', 'a3s',
        'a2s', 'q9s', 't8s', '97s', '87s', '76s', 'kjo', 'qjo', 'jto'
      ];
      constants.tiers[6] = [
        '66', '55', 'k9s', 'j8s', '86s', '75s', '54s', 'ato',
        'kto', 'qto'
      ];
      constants.tiers[7] = [
        '44', '33', '22', 'k8s', 'k7s', 'k6s', 'k5s', 'k4s',
        'k3s', 'k2s', 'q8s', 't7s', '65s', '64s', '53s', '43s', 'j9o',
        't9o', '98o'
      ];
      constants.tiers[8] = [
        'j7s', '96s', '85s', '74s', '42s', '32s', 'a9o', 'k9o',
        'q9o', 'j8o', 't8o', '87o', '76o', '65o', '54o'
      ];

      this.tier1 = constants.tiers[1].join(',');
      this.tier2 = constants.tiers[2].join(',');
      this.tier3 = constants.tiers[3].join(',');
      this.tier4 = constants.tiers[4].join(',');
      this.tier5 = constants.tiers[5].join(',');
      this.tier6 = constants.tiers[6].join(',');
      this.tier7 = constants.tiers[7].join(',');
      this.tier8 = constants.tiers[8].join(',');

      localStorage.setItem('tier1', this.tier1);
      localStorage.setItem('tier2', this.tier2);
      localStorage.setItem('tier3', this.tier3);
      localStorage.setItem('tier4', this.tier4);
      localStorage.setItem('tier5', this.tier5);
      localStorage.setItem('tier6', this.tier6);
      localStorage.setItem('tier7', this.tier7);
      localStorage.setItem('tier8', this.tier8);

      alert('Saved!');
    },
    save: function () {
      if (!areCustomTiersValid([
        this.tier1, this.tier2, this.tier3, this.tier4,
        this.tier5, this.tier6, this.tier7, this.tier8
      ])) {
        alert('One or more custom tiers are invalid.');
        return;
      }

      this.tier1 = this.tier1.toLowerCase().replace(/, /g, ',');
      this.tier2 = this.tier2.toLowerCase().replace(/, /g, ',');
      this.tier3 = this.tier3.toLowerCase().replace(/, /g, ',');
      this.tier4 = this.tier4.toLowerCase().replace(/, /g, ',');
      this.tier5 = this.tier5.toLowerCase().replace(/, /g, ',');
      this.tier6 = this.tier6.toLowerCase().replace(/, /g, ',');
      this.tier7 = this.tier7.toLowerCase().replace(/, /g, ',');
      this.tier8 = this.tier8.toLowerCase().replace(/, /g, ',');

      localStorage.setItem('tier1', this.tier1);
      localStorage.setItem('tier2', this.tier2);
      localStorage.setItem('tier3', this.tier3);
      localStorage.setItem('tier4', this.tier4);
      localStorage.setItem('tier5', this.tier5);
      localStorage.setItem('tier6', this.tier6);
      localStorage.setItem('tier7', this.tier7);
      localStorage.setItem('tier8', this.tier8);

      constants.tiers[1] = this.tier1.split(',');
      constants.tiers[2] = this.tier2.split(',');
      constants.tiers[3] = this.tier3.split(',');
      constants.tiers[4] = this.tier4.split(',');
      constants.tiers[5] = this.tier5.split(',');
      constants.tiers[6] = this.tier6.split(',');
      constants.tiers[7] = this.tier7.split(',');
      constants.tiers[8] = this.tier8.split(',');

      alert('Saved!');
    }
  }
}
</script>

<style lang="scss" scoped>
</style>



